import { Injectable } from "@angular/core";
import { MyHttpService } from "./http.service";
import { StorageService } from "./storage.service";
import { Menu, Group, QueryObject, PageParameter, QueryCondition, QueryParameter } from "../../../share_platform/framework/entity";
import { OssFile, Member, Customer } from "../../../share_platform/market/entity";
import { appCustomerService } from "./app_customer.service";
import { appMemberAccountService } from "./app_member_account.service";
import { EntityEnum } from "../../../share_platform/framework/enum";

declare var QRCode
export enum roleType {
  cusType,
  empType,
  memType
}
@Injectable()

export class CommonService {

  phoneReg: RegExp = /^((1[3,5,8][0-9])|(14[5,7])|(17[0,6,7,8])|(19[7]))\d{8}$/;
  /**密码强度正则，最少6位，包括至少1个大写字母，1个小写字母，1个数字，1个特殊字符*/
  passWordReg: RegExp = /^.*(?=.{6,})(?=.*\d)(?=.*[A-Z])(?=.*[a-z])(?=.*[!@#$%^&*? ]).*$/
  defaultUserAvatar = "/assets/images/123.jpg";
  public commonApi = {
    getQrcode: "/common/qrcode",
    uploadImage: "/common/upload-image",
    uploadFile: "/common/upload/file",
    localCityJson: '/assets/city.json',
    singleTableQuery: "/app/stq/entity/query"
  };

  /**
  .get("/common/qrcode", common.getQrcode)
  .post("/common/qrcode", common.getQrcode)
  .get("/wechat/ticket", common.getTicket)
  .get("/common/wechat/ticket", common.getTicket)
  .post("/common/upload/image", common.uploadBase64Test)
  .get("/common/send-auth-code", common.sendAuthCode);
 */


  /**单表查询 */

  async singleTableQuery(className: EntityEnum, queryObject: QueryObject | QueryObject[], pageParameter?: PageParameter): Promise<{ rows: any[], count: number }> {
    let queryParameter = this.getQueryParameter(queryObject);
    queryParameter.pageParameter = pageParameter
    let result = await this.http.Post(this.commonApi.singleTableQuery, queryParameter, { params: { className } });
    return result.result
  }

  getQueryParameter(queryObject: QueryObject | QueryObject[], pageParameter?: PageParameter): QueryParameter {


    let conditions: QueryCondition[] = [];
    if (Array.isArray(queryObject)) {
      conditions = QueryObject.Or(queryObject)
    } else {
      conditions = QueryObject.toQueryContions(Object.assign(queryObject, new QueryObject()));
    }
    console.log(conditions)
    return { pageParameter: pageParameter ? pageParameter : new PageParameter(), queryAttributes: [] }
  }














  checkPhone(phone: string) {
    return this.phoneReg.test(phone)
  }
  checkPassword(password: string) {
    return this.passWordReg.test(password)
  }
  localCityJson() {
    return this.http.localGetJSON(this.commonApi.localCityJson)
  }
  getQrcode(url) {
    return this.http.Get(this.commonApi.getQrcode, { params: { url } });
  }
  async uploadImage(base64: string): Promise<OssFile> {
    let result = await this.http.Post(this.commonApi.uploadImage, { base64 });
    return result.result
  }

  /**
   *
   * @param url   file
   *
   * @param outputFormat string
   *
   * 将文件转成base64
   */
  compressBase64(
    base64: string,
    maxsize: number = 40000,
    outputFormat = "image/png"
  ): Promise<string> {
    return new Promise((resolve, reject) => {
      var canvas = document.createElement("canvas");
      var ctx = canvas.getContext("2d");
      var img = new Image();
      img.crossOrigin = "Anonymous";
      img.onload = function () {
        var width = img.width;
        var height = img.height;
        let compress = 1;
        let rate = 1;
        if (width * height > maxsize) {
          rate = Math.ceil(width * height / 40000);
        }
        compress = 1 / rate;
        canvas.width = width * compress;
        canvas.height = height * compress;
        ctx.drawImage(
          img,
          0,
          0,
          width,
          height,
          0,
          0,
          width * compress,
          height * compress
        );
        let compressData = canvas.toDataURL(outputFormat);
        resolve(compressData);
      };
      img.src = base64;
    });
  }

  convertFileToBase64(file: File): Promise<string> {
    let reader = new FileReader();
    return new Promise((resolve, reject) => {
      reader.onload = e => {
        let base64: string = <string>e.target["result"];
        resolve(base64);
      };
      reader.readAsDataURL(file);
    });
  }

  // 自动化创建文件文本框,并选择读取base64,可以自定义压缩
  selectFile(): Promise<string> {
    return new Promise(resolve => {
      let fileInputEle = document.createElement("input");
      fileInputEle.type = "file";
      let reader = new FileReader();
      reader.onload = event => {
        let base64 = <any>event.target["result"];
        resolve(base64);
      };

      fileInputEle.onchange = event => {
        reader.readAsDataURL(fileInputEle.files[0]);
      };
      fileInputEle.click();
    });
  }


  getMenuTree(menus: Menu[]): Menu[] {
    let treeMenus: Menu[] = [];
    /** 扫出顶级菜单,若存在下级菜单递归扫下级菜单 */
    if (menus.some(menu => menu.parentId == 0 || !menu.parentId)) {
      treeMenus = menus.filter(menu => menu.parentId == 0 || !menu.parentId);
      for (let menu of treeMenus) {
        menu.children = this.getMenuChildren(menu, menus);
      }
      return treeMenus;
    }
  }


  private getMenuChildren(parentMenu: Menu, menus: Menu[]): Menu[] {
    let children = menus.filter(menu => menu.parentId == parentMenu.menuId);
    for (let submenu of children) {
      submenu.children = this.getMenuChildren(submenu, menus);
    }
    return children;

  }
  getModuleGroup(modules: Menu[]) {
    let groups: Group[] = [];

    modules.forEach(modu => {
      let isChild = modules.find(
        mcurrent => mcurrent.menuId == modu.menuId && modu.menuId != 0
      );
      /**
       * 属于子模块并且子模块已存在分组中
       * 此时
       *
       */

      let existGroup = groups.find(
        group => group.menuId == modu.parentId
      );
      groups.find(group => {
        // console.log(group.id, modu.id);
        return group.menuId == modu.menuId;
      });
      // ), groups)};
      if (isChild && existGroup) {
        // console.log(`pushc child`);
        existGroup.children.push({
          text: modu.text,
          label: modu.text,
          menuId: modu.menuId,
          link: modu.link,
          checked: !!modu.checked,
          icon: modu.icon
        });
      } else {
        groups.push({
          text: modu.text,
          menuId: modu.menuId,

          checked: !!modu.checked,
          icon: modu.icon,

          checkdAll: false,
          children: []
        });
        console.log(groups);
      }
    });
    return groups;
  }
  getCheckedMenuIds(groups: Group[]): number[] {
    console.log(groups)
    let gcMenuIds = [];
    groups.forEach(group => {
      if (group.checked || group.indeterminate) {
        gcMenuIds.push(group.menuId);
        if (group.children) {
          group.children.forEach(menu => {
            menu.checked ? gcMenuIds.push(menu.menuId) : '';
          })
        }
      }
    })
    return gcMenuIds;
  }

  menuIdsToMenus(menuIds: number[], menus: Menu[]): Menu[] {
    return menuIds.filter(id => id).map(id => menus.find(menu => menu.menuId == id));
  }

  /**获取用户身份 */

  member: Member = this.storage.member
  customer: Customer = this.storage.customer

  async getRole() {
    if (this.member) {

      if (this.member.customerId == this.customer.id) {
        return roleType.memType
      } else {
        return roleType.empType
      }
    } else {

      return roleType.cusType
    }
  }
  /**跟新缓存 */
  async  updateSto() {
    if (this.storage.customer) {

      let result = await this.cusService.customerDetail(this.storage.customer.id)

      this.storage.customer = result

    }
    if (this.storage.customer.memberId) {
      this.storage.member = await this.memService.memberDetail()
    } else {
      this.storage.member = {}
    }

  }




  constructor(public http: MyHttpService, public storage: StorageService,
    public cusService: appCustomerService,
    public memService: appMemberAccountService,



  ) { }
}



