import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { CommonService } from '../../app/share/service/common.service';
import { appCommonService } from '../../app/share/service/app_common.service';
import { appCustomerService } from '../../app/share/service/app_customer.service';
import { Customer } from '../../share_platform/market/entity';
import { StorageService } from '../../app/share/service/storage.service';
import { MyHttpService } from '../../app/share/service/http.service';
import { ModifyNewPhonePage } from '../modify-new-phone/modify-new-phone';

/**
 * Generated class for the ModifyPhonePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

// @IonicPage()
@Component({
  selector: 'page-modify-phone',
  templateUrl: 'modify-phone.html',
})
export class ModifyPhonePage {
  password: string;
  cus: Customer
  constructor(public navCtrl: NavController, public navParams: NavParams,
    public cusSer: appCustomerService,
    public sto: StorageService,
    public http: MyHttpService
  ) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ModifyPhonePage');
  }
  async goNext() {
    let passwordRex = /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,}$/
    if (!(passwordRex.test(this.password))) {
      this.http.createMessage("error", "密码至少八个字符，至少一个字母和一个数字")


    } else {
      this.cus = this.sto.customer
      this.cus.password = this.password

      let result = await this.cusSer.authPassword(this.cus)
      if (result) {
        debugger
        this.navCtrl.push(ModifyNewPhonePage)
        debugger
      } else {
        this.http.createMessage("warning", "密码不正确，请重新输入")
        this.password = ""
      }
    }


  }
}



