import { Pipe, PipeTransform } from '@angular/core';
import { PriceUnitEnum, PriceWayEnum } from '../../../share_platform/market/enum';
/*
 * Raise the value exponentially
 * Takes an exponent argument that defaults to 1.
 * Usage:
 *   value | exponentialStrength:exponent
 * Example:
 *   {{ 2 | exponentialStrength:10 }}
 *   formats to: 1024
*/
@Pipe({ name: 'priceWayPipePipe' })
export class PriceWayPipePipe implements PipeTransform {


    transform(value: PriceWayEnum, exponent: string): string {
        if (value == PriceWayEnum.Piece) {


            return "计件";


        } else {
            return "计重"
        }
    }
}


