import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { CommonService } from '../../app/share/service/common.service';
import { appCommonService } from '../../app/share/service/app_common.service';
import { VerifyType } from '../../share_platform/framework/enum';
import { ResetPayPassword3Page } from '../reset-pay-password3/reset-pay-password3';
import { StorageService } from '../../app/share/service/storage.service';
import { MyHttpService } from '../../app/share/service/http.service';

/**
 * Generated class for the ResetPayPassword2Page page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-reset-pay-password2',
  templateUrl: 'reset-pay-password2.html',
})
export class ResetPayPassword2Page {

  constructor(public navCtrl: NavController, public navParams: NavParams,
    public comm: appCommonService,
    public sto: StorageService,
    public http: MyHttpService,


  ) {

  }
  mktId: number;
  sendBool: boolean = true;
  looptime: number = 0;
  phone: string = '';
  authcode;
  v = VerifyType.wallet
  ionViewDidLoad() {
    console.log('ionViewDidLoad ResetPayPassword2Page');
  }
  async  sendAuthcode() {
    let phoneReg: RegExp = /^((1[3,5,8][0-9])|(14[5,7])|(17[0,6,7,8])|(19[7]))\d{8}$/;
    if (!this.phone) {
      this.http.createMessage("warning", "请输入手机号")
    } else {
      if (!(phoneReg.test(this.phone))) {
        this.http.createMessage("error", "请输入正确手机号")
      } else {

        let result = await this.comm.authcode(this.sto.customer.mktId, this.phone, this.v);
        if (result) {
          this.looptime = 60;
          let timmer = setInterval(() => {
            this.looptime -= 1;
            if (this.looptime <= 0) {
              this.sendBool = true
              clearInterval(timmer);
            }

          }, 1000);
        } else {
          this.sendBool = true
        }
      }
    }
  }
  async goNext() {
    this.navCtrl.push(ResetPayPassword3Page)
  }
}



