import { Pipe, PipeTransform } from '@angular/core';
import { MemberInviteStatus, OrderTypeEnum } from '../../../share_platform/market/enum';
/*
 * Raise the value exponentially
 * Takes an exponent argument that defaults to 1.
 * Usage:
 *   value | exponentialStrength:exponent
 * Example:
 *   {{ 2 | exponentialStrength:10 }}
 *   formats to: 1024
*/
@Pipe({ name: 'orderTypePipePipe' })
export class OrderTypePipePipe implements PipeTransform {
    orderType: OrderTypeEnum
    transform(value: OrderTypeEnum, exponent: string) {






        if (value == OrderTypeEnum.TRANS) {


            return "交易";


        }

        if (value == OrderTypeEnum.RENTS) {
            return "租金"
        }



    }
}


