import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, App } from 'ionic-angular';
import { WeighSetupPage } from "../weigh-setup/weigh-setup";
import { PrintingSetupPage } from "../printing-setup/printing-setup";
import { VoiceSetupPage } from "../voice-setup/voice-setup";
import { AboutUsPage } from "../about-us/about-us";
import { MktlistPage } from "../mktlist/mktlist"
import { StorageService } from '../../app/share/service/storage.service';
import { Market } from '../../share_platform/framework/entity';
import { Member, Customer } from '../../share_platform/market/entity';
import { BLE } from '@ionic-native/ble';
/**
 * Generated class for the AppSetupPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

// @IonicPage()
@Component({
  selector: 'page-app-setup',
  templateUrl: 'app-setup.html',
})
export class AppSetupPage {
  market: Market = {

  }
  member: Member = {}
  customer: Customer = {}
  constructor(public navCtrl: NavController, public navParams: NavParams,
    public stor: StorageService,
    public ble: BLE,
    private app: App
  ) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad AppSetupPage');
  }
  weightSignup() {
    this.navCtrl.push(WeighSetupPage)
  }
  printSet() {
    this.navCtrl.push(PrintingSetupPage)

  }
  voiceSetup() {
    this.navCtrl.push(VoiceSetupPage)
  }
  goAbout() {
    this.navCtrl.push(AboutUsPage)
  }
  breakApp() {

    localStorage.clear()
    this.app.getRootNav().setRoot(MktlistPage)

  }
}



