import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Events } from 'ionic-angular';
import { ProductAddPage } from "../product-add/product-add";
import { StorageService } from '../../app/share/service/storage.service';
import { OpenBillPage } from '../open-bill/open-bill';
import { Product } from '../../share_platform/market/entity';
import { appProductOrderService } from '../../app/share/service/app_ProductOrder.service';
import { pinyinOrderService } from '../../app/share/service/pinyinOrder.service'

/**
 * Generated class for the SelsetProductPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

// @IonicPage()
@Component({
  selector: 'page-selset-product',
  templateUrl: 'selset-product.html',
})
export class SelsetProductPage {

  porductIndex;
  products: Product[] = []
  productlist = []
  producted

  productList = [];

  constructor(public navCtrl: NavController, public navParams: NavParams,
    public str: StorageService,
    public events: Events,
    public product: appProductOrderService,
    public pinyin: pinyinOrderService
  ) {
    this.porductIndex = this.navParams.get('productIndex');
  }



  async ionViewDidLoad() {
    this.products = await this.product.productList()
    console.log(this.products);
    for (let product in this.products) {
      this.productlist.push(`${this.products[product].prodCat.catName}`);
    }
    this.productlist = this.productlist.filter((element, index, array) => {
      return array.indexOf(element) === index;
    })
    this.productlist = this.pinyin.sort(this.productlist);

    for (let productName of this.productlist) {
      this.producted = { title: '', data: [] };
      this.producted.title = productName
      for (let product in this.products) {
        if (`${this.products[product].prodCat.catName}` == `${productName}`) {
          this.producted.data.push(this.products[product]);
          console.log(this.producted)
        }
      }
      this.productList.push(this.producted);
      console.log(this.productList)
    }
  }




  goBack(str) {
    this.navCtrl.pop().then(() => {
      if (this.porductIndex == undefined) {
        this.events.publish('hotkey', str)
      } else {
        this.events.publish('hotkey', str, this.porductIndex)
      }

    })
  }

  addProduct() {
    this.navCtrl.push(ProductAddPage)
  }
} 



