import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, PopoverController, Events } from 'ionic-angular';

import { appCustomerService } from '../../app/share/service/app_customer.service';
import { Customer, Member, Account } from '../../share_platform/market/entity';
import { OpeningWallet1Page } from "../opening-wallet1/opening-wallet1"
import { StorageService } from '../../app/share/service/storage.service';
import { appMessageService } from '../../app/share/service/app_message.service';
import { appMemberAccountService } from '../../app/share/service/app_member_account.service';
import { SetMoneyPage } from '../set-money/set-money';
import { CustomerStatusEnum, MemberStatusEnum } from '../../share_platform/market/enum';
import { AuthenticationPage } from "../authentication/authentication"
import { ResetPayPasswordPage } from '../reset-pay-password/reset-pay-password';
import { WalletCardPage } from '../wallet-card/wallet-card';
import { WalletTransferPage } from '../wallet-transfer/wallet-transfer';
import { WalletTransferListPage } from '../wallet-transfer-list/wallet-transfer-list';
import { WalletFriendPage } from '../wallet-friend/wallet-friend';
import { WalletFriendListPage } from '../wallet-friend-list/wallet-friend-list';
import { MyHttpService } from '../../app/share/service/http.service';
import { BarcodeScannerPage } from '../barcode-scanner/barcode-scanner';
import { WalletExpendPage } from '../wallet-expend/wallet-expend';
/**
 * Generated class for the WalletPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */



// @IonicPage()
@Component({
  selector: 'page-wallet',
  templateUrl: 'wallet.html',
})
export class WalletPage {

  customer: Customer
  member: Member
  account: Account
  isShowPop: boolean = false
  memStatus = MemberStatusEnum
  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public popoverCtrl: PopoverController,
    public cus: appCustomerService,
    public mem: appMemberAccountService,
    public sto: StorageService,
    public events: Events,
    public http: MyHttpService
  ) {
    this.events.subscribe('memAccount', (memAccount) => {
      this.account = memAccount

      this.events.unsubscribe('memAccount');
    });
  }
  info() {
    this.navCtrl.push(AuthenticationPage)
  }
  resetPay() {
    this.navCtrl.push(ResetPayPasswordPage)
  }
  async ionViewDidLoad() {
    this.customer = this.sto.customer



    if (this.customer.memberId) {

      this.member = await this.mem.memberDetail(this.customer.id)
      let result = await this.mem.accountDetail()
      this.account = result.account


    }
  }
  /**转账*/
  async TransAccounts() {
    if (this.account.availAmt > 0) {

      this.navCtrl.push(WalletTransferListPage)
    } else {
      this.http.createMessage("warning", "余额不足，请充值")
    }
  }
  /**收款 */
  async receivables() {
    this.navCtrl.push(SetMoneyPage)
  }
  async payment() {
    this.navCtrl.push(BarcodeScannerPage)
  }
  presentPopover() {
    this.isShowPop = !this.isShowPop
  }
  async bandCard() {
    this.navCtrl.push(WalletCardPage)
  }
  ionViewCanLeave() {
    this.isShowPop = false
  }
  async myFriend() {
    this.navCtrl.push(WalletFriendListPage)
  }
  // 交易记录
  async expendOrSend() {
    this.navCtrl.push(WalletExpendPage)
  }
}



