import { Injectable } from "@angular/core";
import { Customer, Member, TransArea, CustCard } from "../../../share_platform/market/entity";
import { Market } from "../../../share_platform/framework/entity";
import { PriceWayEnum } from "../../../share_platform/market/enum";

@Injectable()
export class StorageService {
  get customer(): Customer {
    return JSON.parse(localStorage.getItem('customer'));
  }
  set customer(customer: Customer) {
    localStorage.setItem('customer', JSON.stringify(customer));
  }
  get disabledMsgTypes(): string[] {
    return this.customer.disabledMsgTypes ? this.customer.disabledMsgTypes.split(",") : [];
  }
  get token() {
    return localStorage.getItem('token')
  }
  set token(token: string) {
    localStorage.setItem('token', token);
  }
  get market(): Market {
    return JSON.parse(localStorage.getItem("market"))

  }
  set market(market: Market) {
    localStorage.setItem("market", JSON.stringify(market))
  }
  get member(): Member {
    return JSON.parse(localStorage.getItem("member"))
  }
  set member(member: Member) {
    localStorage.setItem("member", JSON.stringify(member))
  }

  get priceW(): string {
    return JSON.parse(localStorage.getItem("priceW"))
  }
  set priceW(priceW: string) {
    localStorage.setItem("priceW", JSON.stringify(priceW))

  }
  get txn(): TransArea {
    return JSON.parse(localStorage.getItem("txn"))
  }
  set txn(txn: TransArea) {
    localStorage.setItem("txn", JSON.stringify(txn))
  }
  get newMember(): Member {
    return JSON.parse(localStorage.getItem("newMember"))
  }
  set newMember(newMember: Member) {
    localStorage.setItem("newMember", JSON.stringify(newMember))
  }
  set card(card: CustCard) {
    localStorage.setItem("card", JSON.stringify(card))
  }
  get card(): CustCard {
    return JSON.parse(localStorage.getItem("card"))
  }
  //读写
  constructor() { }
}



