import { Component, Input } from '@angular/core';
import { NavController, NavParams, AlertController, App, Events } from 'ionic-angular';
import { MemberInvite, Customer, Member } from '../../share_platform/market/entity';
import { CommonService, roleType } from '../../app/share/service/common.service';
import { memory } from '@angular/core/src/render3/instructions';
import { appTeamService } from '../../app/share/service/app_team.service';
import { MyHttpService } from '../../app/share/service/http.service';
import { StorageService } from '../../app/share/service/storage.service';
import { MyTeamPage } from '../my-team/my-team';

/**
 * Generated class for the Register1Page page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

// @IonicPage()



@Component({
    selector: 'page-memberInvite',

    templateUrl: 'memberInvite.html',
})
export class MemberInvitePage {
    @Input() selectInv: MemberInvite
    userRole: roleType

    viewRoleType = roleType;
    myTeams: Customer[] = []
    constructor(public navCtrl: NavController,
        public navParams: NavParams,
        public comm: CommonService,
        public teamSeivice: appTeamService,
        public http: MyHttpService,
        public sto: StorageService,

        private app: App,
        public events: Events
    ) {
        this.user()





    }

    ionViewDidLoad() {

    }
    async  user() {
        this.userRole = await this.comm.getRole()
        console.log(this.userRole)
        debugger
    }
    async deleteInv() {
        await this.teamSeivice.disabledInvite(this.selectInv.id)
        this.http.createMessage("success", "删除成功")
        this.navCtrl.pop()
    }
    async agreeInvit() {
        debugger
        await this.teamSeivice.acceptInvite(this.selectInv.id)
        this.http.createMessage("success", "已同意邀请")
        await this.comm.updateSto()
        this.myTeams = await this.teamSeivice.teamList()
        this.navCtrl.pop().then(() => {
            this.events.publish("data", this.myTeams)
        })
    }
    async   refuseInvit() {

        await this.teamSeivice.refuseInvite(this.selectInv.id)
        this.http.createMessage("success", "已拒绝邀请")
        this.navCtrl.pop()

    }
}



