import { Component } from '@angular/core';
import { NavController, NavParams, Platform, ActionSheetController, Events, AlertController } from 'ionic-angular';

import { BlueListPage } from '../../pages/blue-list/blue-list';
import { PriceUnitEnum } from '../../share_platform/market/enum';
import { StorageService } from '../../app/share/service/storage.service';
import { BLE } from '@ionic-native/ble';



/**
 * Generated class for the WeighSetupPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

// @IonicPage()
@Component({
  selector: 'page-weigh-setup',
  templateUrl: 'weigh-setup.html',
})
export class WeighSetupPage {
  device
  status
  selectPriceW = PriceUnitEnum.JIN
  priceUnit = PriceUnitEnum
  constructor(public navCtrl: NavController, public navParams: NavParams, public platform: Platform,
    public actionsheetCtrl: ActionSheetController,
    public ble: BLE,
    public event: Events,
    // public alert: AlertController,
    public sto: StorageService
  ) {


  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad WeighSetupPage');
    if (!this.sto.priceW) {
      this.sto.priceW = this.priceUnit.JIN
    }
  }

  //选择蓝牙设备



  ok() {
    this.navCtrl.pop()
  }

  changePriceW() {
    this.sto.priceW = ""
    this.sto.priceW = this.selectPriceW

  }

  blueList() {
    let device = 'scales'
    this.navCtrl.push(BlueListPage, { device: device })
  }

}



