import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { MyTransactionDetailPage } from "../my-transaction-detail/my-transaction-detail";
import { CommonService } from '../../app/share/service/common.service';
import { EntityEnum } from '../../share_platform/framework/enum';

/**
 * Generated class for the MyTransactionPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

// @IonicPage()
@Component({
  selector: 'page-my-transaction',
  templateUrl: 'my-transaction.html',
})
export class MyTransactionPage {
  startDt: any = new Date().toISOString();
  endDt: any = new Date().toISOString();
  loadingCompleted: boolean = false;
  order;
  orderList = [];
  index = 1;

  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public common: CommonService,
  ) {


  }

  async ionViewDidLoad() {
    let res = await this.common.singleTableQuery(EntityEnum.Order, { createTime: { $gt: this.startDt, $lt: this.endDt }, pageIndex: 0, pageSize: 5 })
    this.orderList = res['rows'];
  }
  public event = {
    month: '1990-02-19',
    timeStarts: '07:43',
    timeEnds: '1990-02-20'
  }
  goDetail() {
    this.navCtrl.push(MyTransactionDetailPage)
  }





  async doRefresh(refresher) {
    console.log('Begin async operation', refresher);
    setTimeout(() => {
      this.orderList = []
      let res = this.common.singleTableQuery(EntityEnum.Order, { createTime: { $gt: this.startDt, $lt: this.endDt }, pageIndex: 0, pageSize: 5 })
      this.orderList = res['rows'];
      this.index = 1;
      refresher.complete();
    }, 2000);
  }

  async doInfinite(infiniteScroll) {
    console.log('Begin async operation');
    setTimeout(() => {
      let res = this.common.singleTableQuery(EntityEnum.Order, { createTime: { $gt: this.startDt, $lt: this.endDt }, pageIndex: (this.index * 5), pageSize: 5 })
      if (res['rows'] = []) {
        this.loadingCompleted = true
      } else {
        this.orderList = this.orderList.concat(res['rows']);
        this.index = this.index + 1
        console.log('Async operation has ended');
      }
      infiniteScroll.complete();
    }, 500);
  }
}





