import { MyHttpService } from "./http.service";
import { Injectable } from "@angular/core";
import { Market } from "../../../share_platform/framework/entity";

@Injectable()
export class appCommonService {
    private api = {
        marketList: '/app/common/market-list',
        /**
         * get
         * ?phone&authcode&verifyType&mktId
         */
        checkAuthcode: '/app/common/check-authcode',
        /**
         * get
         * ?phone&verifyType&mktId
         */
        sendAuthcode: '/app/common/send-authcode'

    }
    async authcode(mktId: number, phone: string, verifyType: string) {
        let result = await this.http.Get(this.api.sendAuthcode, { params: { phone, mktId, verifyType } });
        return result
    }
    async marketList(): Promise<Market[]> {
        let result = await this.http.Get(this.api.marketList);
        if (result.markets) {
            return result.markets
        }
    }
    async checkAuthcode(phone: string, authcode: string, mktId: number, verifyType: string) {
        return this.http.Get(this.api.checkAuthcode, { params: { phone, authcode, mktId, verifyType } });
    }
    constructor(public http: MyHttpService) {

    }
}


