import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { MsgTypeEnum } from '../../share_platform/market/enum';
import { appMessageService } from '../../app/share/service/app_message.service';
import { StorageService } from '../../app/share/service/storage.service';

/**
 * Generated class for the MessageSetPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

// @IonicPage()
@Component({
  selector: 'page-message-set',
  templateUrl: 'message-set.html',
})
export class MessageSetPage {
  nofitication: string[] = [];
  nofiticationS: string = '';

  msgs = {
    ACC_AMT: true,
    CUST_INVITE: true,
    ACTOR_CHANGE: true,
    REPAY_APPLY: true,
    REPAY_CONFIRM: true,
    TOBE_PAY: true
  }
  constructor(public navCtrl: NavController, public navParams: NavParams, public Msg: appMessageService, public storge: StorageService) {
    this.nofitication = this.storge.disabledMsgTypes || []
    this.nofitication.forEach((value, index, arr) => {
      for (let msg in this.msgs) {
        if (`${msg}` == value) {
          this.msgs[msg] = false;
        }
      }
    });
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad MessageSetPage');
  }




  async save() {
    for (let msg in this.msgs) {
      if (this.msgs[msg] == false) {
        this.nofiticationS = this.nofitication + ',' + msg
      }
    }
    console.log(this.nofiticationS)
    await this.Msg.msgManage(this.nofiticationS)
    this.navCtrl.pop()
  }


}



