import { Component } from '@angular/core';
import {
  //  IonicPage,
  NavController, NavParams
} from 'ionic-angular';
import { appTeamService } from '../../app/share/service/app_team.service';
import { Customer } from '../../share_platform/market/entity';
import { TeamAddDetailPage } from '../team-add-detail/team-add-detail';
import { MyHttpService } from '../../app/share/service/http.service';

/**
 * Generated class for the AddTeamerPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

// @IonicPage()
@Component({
  selector: 'page-add-teamer',
  templateUrl: 'add-teamer.html',
})
export class AddTeamerPage {
  serchPhone: number
  newCus: Customer
  hasCus: boolean = false
  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public teamService: appTeamService,
    public http: MyHttpService
  ) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad AddTeamerPage');
  }
  async serch() {
    this.newCus = {}
    let result = await this.teamService.searchCustomerByPhone(this.serchPhone)
    if (result) {

      if (result.memberId) {
        this.http.createMessage("warning", "已加入团队，请更换其他用户")
      }
      this.newCus = result
      this.hasCus = true

    } else {
      this.http.createMessage("warning", "暂无数据")
    }


  }
  async add() {
    if (this.hasCus) {
      debugger
      this.navCtrl.push(TeamAddDetailPage, this.newCus)
    }


  }
}


