import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Events } from 'ionic-angular';
import { MsgTypeEnum } from '../../share_platform/market/enum';
import { AppMsgNotify } from '../../share_platform/market/entity';
import { appMessageService } from '../../app/share/service/app_message.service';
import { WalletFriendDetailPage } from '../wallet-friend-detail/wallet-friend-detail';
import { WalletExpendDetailPage } from '../wallet-expend-detail/wallet-expend-detail';


/**
 * Generated class for the MessageWaitPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

// @IonicPage()


@Component({
  selector: 'page-message-wait',
  templateUrl: 'message-wait.html',
})
export class MessageWaitPage {
  typed: string = ''
  type = '';
  msgList = [];
  notification
  simpleColumns: any[];
  msgType: MsgTypeEnum
  cusMsg: AppMsgNotify
  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public msg: appMessageService,
    public event: Events
  ) {
    this.simpleColumns = [
      {
        name: 'col1',
        options: [
          { text: '全部', value: MsgTypeEnum.All },
          { text: '账户变动信息', value: MsgTypeEnum.ACC_AMT },
          { text: '邀请信息', value: MsgTypeEnum.CUST_INVITE },
          { text: '操作员变动信息', value: MsgTypeEnum.ACTOR_CHANGE },
          { text: '待付款', value: MsgTypeEnum.TOBE_PAY },
          { text: '现金还款通知', value: MsgTypeEnum.REPAY_APPLY },
          { text: '现金还款确认', value: MsgTypeEnum.REPAY_CONFIRM },
        ]
      }]
  }

  async ionViewDidLoad() {

    let result = await this.msg.msgQuery(MsgTypeEnum.All);
    debugger
    this.msgList = result
  }

  async list(type) {
    if (type != this.typed) {
      this.typed = type
      let result = await this.msg.msgQuery(type);
      debugger
      this.msgList = result
    }
  }

  goDetail(msg) {
    this.cusMsg = msg
    if (this.cusMsg.msgType == MsgTypeEnum.ACC_AMT) {
      // 账户变动

      this.navCtrl.push(WalletExpendDetailPage, JSON.parse(this.cusMsg.addition))
    }
    if (this.cusMsg.msgType == MsgTypeEnum.ACTOR_CHANGE) {
      // 操作员变动
    }
    if (this.cusMsg.msgType == MsgTypeEnum.CUST_INVITE) {

      this.navCtrl.push(WalletFriendDetailPage, this.cusMsg)
      debugger
    }
    if (this.cusMsg.msgType == MsgTypeEnum.REPAY_APPLY) {
      // 现金还款
    }
    if (this.cusMsg.msgType == MsgTypeEnum.REPAY_CONFIRM) {
      // 现金还款确认
    }
    if (this.cusMsg.msgType == MsgTypeEnum.TOBE_PAY) {
      // 待付款
    }
  }



}



