import { Component } from '@angular/core';
import { NavController, NavParams, ViewController, ModalController, Alert } from 'ionic-angular';
import { QRScanner, QRScannerStatus } from '@ionic-native/qr-scanner';
import { CustomerPage } from '../customer/customer';
import { appProductOrderService } from '../../app/share/service/app_ProductOrder.service';
import { payStatus, PayTypeEnum, OrderStatusEnum } from '../../share_platform/market/enum';
import { OrderPayPage } from "../../pages/order-pay/order-pay" //扫码支付详情--确认支付
import { Observable } from "RxJS/Rx";
import { MyHttpService } from '../../app/share/service/http.service';

/**
 * Generated class for the BarcodeScannerPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-barcode-scanner',
  templateUrl: 'barcode-scanner.html',
})
export class BarcodeScannerPage {
  light: boolean;//判断闪光灯
  frontCamera: boolean;//判断摄像头
  isShow: boolean = false;//控制显示背景，避免切换页面卡顿
  orderNo: string;
  businessType: string;
  model: boolean = false;
  getting: boolean = true;
  payType;
  qcodeBizType
  querOrderStatus;
  viewPage = true;
  OrderDetail;

  show = false;


  constructor(
    private navCtrl: NavController,
    private navParams: NavParams,
    private qrScanner: QRScanner,
    public modelCtrl: ModalController,
    public order: appProductOrderService,
    public http: MyHttpService,
    private viewCtrl: ViewController) {

    this.httpRes('TTAwMDEwMDMyOSxUUkFOU19QQVk=')
    //默认为false
    this.light = false;
    this.frontCamera = false;
  }

  ionViewDidLoad() {
    this.qrScanner.prepare()
      .then((status: QRScannerStatus) => {
        if (status.authorized) {

          // 开始扫描
          let scanSub = this.qrScanner.scan().subscribe((text: string) => {

            this.httpRes(text);

            this.qrScanner.hide();//假的关闭 不能用
            scanSub.unsubscribe(); // 停止扫描
            // this.navCtrl.pop();  //跳转页面
          });

          // 相机可见
          this.qrScanner.show();

        } else if (status.denied) {

        } else {

        }
      })
      .catch((e: any) => console.log('Error is', e));
  }

  ionViewDidEnter() {
    //页面可见时才执行
    this.showCamera();
    this.isShow = true;//显示背景
  }


  async httpRes(text) {
    alert(text)
    let res = await this.order.exeQCode(text);
    if (res) {
      this.qcodeBizType = res['qcodeBizType']
      this.orderNo = res['params']['orderNo']
      this.payType = res['params']['payType']
      console.log('1' + this.qcodeBizType, this.orderNo, this.payType);
    } else {
      this.http.createMessage('warning', '请求失败');
    }

    if (this.qcodeBizType == 'TRANS_PAY') {

      if (this.payType == PayTypeEnum.FS_C2B) {

        this.navCtrl.push(OrderPayPage, { res: this.orderNo });

      } else if (this.payType == PayTypeEnum.FS_CREDIT) {
        //第一步扫码轮询卖家是不是同意了
        this.show = true;
        this.querOrderStatus = Observable.interval(5000).startWith(1) //消除延时
          .mergeMap(() => this.order.querOrderStatus(this.orderNo))
          .subscribe((rtn) => {
            if (rtn.status == OrderStatusEnum.SELLER_AGREE) { //返回卖家同意
              this.show = false
              this.navCtrl.push(OrderPayPage, { res: this.orderNo });
              this.querOrderStatus.unsubscribe(); //取消订阅 来停止轮询
            } else if (rtn.status == OrderStatusEnum.CLOSED) { //返回卖家拒绝 
              this.http.createMessage('warning', '卖家拒绝交易')
              this.querOrderStatus.unsubscribe(); //取消订阅 来停止轮询
            }

            if (this.viewPage == false) {
              this.querOrderStatus.unsubscribe();
            }
          })

      }

    } else {

    }
  }



  /**
   * 闪光灯控制，默认关闭
   */
  toggleLight() {
    if (this.light) {
      this.qrScanner.disableLight();
    } else {
      this.qrScanner.enableLight();
    }
    this.light = !this.light;
  }

  /**
   * 前后摄像头互换
   */
  toggleCamera() {
    if (this.frontCamera) {
      this.qrScanner.useBackCamera();
    } else {
      this.qrScanner.useFrontCamera();
    }
    this.frontCamera = !this.frontCamera;
  }

  showCamera() {
    (window.document.querySelector('ion-app') as HTMLElement).classList.add('cameraView');
  }
  hideCamera() {
    (window.document.querySelector('ion-app') as HTMLElement).classList.remove('cameraView');
    this.qrScanner.hide();//需要关闭扫描，否则相机一直开着
  }

  ionViewDidLeave() {
    this.viewPage = false;
    this.querOrderStatus.unsubscribe;
    this.hideCamera();
    this.qrScanner.destroy();//销毁相机
  }
}




