import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ProductEditPage } from "../product-edit/product-edit";
import { ProductAddPage } from "../product-add/product-add"
import { Product } from '../../share_platform/market/entity';
import { appProductOrderService } from '../../app/share/service/app_ProductOrder.service';
import { PriceUnitEnum, ProductStatusEnum, PriceWayEnum } from '../../share_platform/market/enum';
import { MyHttpService } from '../../app/share/service/http.service';
import { CommonService } from '../../app/share/service/common.service';
import { EntityEnum } from '../../share_platform/framework/enum';
/**
 * Generated class for the ProductListPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

// @IonicPage()
enum View {
  serchPro,
  defulPro

}
@Component({
  selector: 'page-product-list',
  templateUrl: 'product-list.html',
})
export class ProductListPage {
  products: Product[];
  View = View;
  state = View.defulPro;
  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public prod: appProductOrderService,
    public http: MyHttpService,
    public comSer: CommonService
  ) {




  }
  startDt: any = new Date().toISOString();
  endDt: any = new Date().toISOString();
  proW = PriceUnitEnum
  productStatus = ProductStatusEnum
  selsctStatus
  keyWord
  selectPriceWay = PriceWayEnum
  b_proList = EntityEnum.Product
  async ionViewCanEnter() {
    if (this.selsctStatus) {
      this.products = await this.prod.productListByStatus(this.selsctStatus)
    } else {
      this.products = await this.prod.productList()
    }
  }
  async ionViewDidLoad() {



  }
  public event = {
    month: '1990-02-19',
    timeStarts: '07:43',
    timeEnds: '1990-02-20'
  }
  async changeList() {
    debugger
    if (this.selsctStatus) {
      this.products = await this.prod.productListByStatus(this.selsctStatus)
    } else {
      this.products = await this.prod.productList()
    }
  }
  /**更新 查看详情 */
  goDetail(product) {
    this.navCtrl.push(ProductEditPage, product)
  }
  /**添加*/
  addProduct() {
    this.navCtrl.push(ProductAddPage)
  }
  back() {
    this.navCtrl.pop()
  }
  /**禁用商品*/
  async deleteProd(product) {
    await this.prod.productDel(product.id)
    this.http.createMessage("success", "删除成功")

    if (this.selsctStatus) {
      this.products = await this.prod.productListByStatus(this.selsctStatus)
    } else {
      this.products = await this.prod.productList()
    }
  }
  /** 关键字搜索*/
  async serchKeyWord($event) {

    let key_code = window.event ? $event.keyCode : $event.which;
    if (key_code == 13) {
      let result = await this.comSer.singleTableQuery(this.b_proList, { name: { $like: this.keyWord } })
      if (result) {
        this.products = result.rows
        this.state = View.defulPro
      } else {
        this.http.createMessage("error", "暂无相关商品")
      }
    }


  }








}



