import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ContactInvite } from '../../share_platform/market/entity';
import { appMemberAccountService } from '../../app/share/service/app_member_account.service';
import { StorageService } from '../../app/share/service/storage.service';
import { MemberInviteStatus } from '../../share_platform/market/enum';

/**
 * Generated class for the WalletFriendDetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-wallet-friend-detail',
  templateUrl: 'wallet-friend-detail.html',
})
export class WalletFriendDetailPage {
  // ContactInvite
  memInvi: ContactInvite
  agreeOrRefus = MemberInviteStatus
  constructor(public navCtrl: NavController, public navParams: NavParams,
    public memS: appMemberAccountService,
    public sto: StorageService
  ) {
    this.memInvi = this.navParams.data
    debugger
  }



  ionViewDidLoad() {
    console.log('ionViewDidLoad WalletFriendDetailPage');
  }
  async agreeInvit() {
    this.memInvi.status = this.agreeOrRefus.Agree
    await this.memS.Inviprocess(this.memInvi)
    this.navCtrl.pop()
  }
  async refuseInvit() {
    this.memInvi.status = this.agreeOrRefus.Refuse
    await this.memS.Inviprocess(this.memInvi)
    this.navCtrl.pop()
  }
}



