import { Component } from '@angular/core';
import { NavController, NavParams, Events } from 'ionic-angular';
import { InvitationPage } from "../invitation/invitation";
import { AddTeamerPage } from "../add-teamer/add-teamer"
import { Customer, Member } from '../../share_platform/market/entity';
import { StorageService } from '../../app/share/service/storage.service';
import { appTeamService } from '../../app/share/service/app_team.service';
import { MyHttpService } from '../../app/share/service/http.service';
import { CommonService } from '../../app/share/service/common.service';
import { NickNamePage } from '../share/nickName';
/**
 * Generated class for the MyTeamPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */


@Component({
  selector: 'page-my-team',
  templateUrl: 'my-team.html',
})
export class MyTeamPage {
  customers: Customer[] = []
  member: Member
  customer: Customer
  myTeams: Customer[] = []
  selectCustomer: Customer
  constructor(public navCtrl: NavController, public navParams: NavParams,
    public sto: StorageService,
    public myTeamService: appTeamService,
    public http: MyHttpService,
    public comm: CommonService,
    public events: Events
  ) {
    this.events.subscribe("data", (myTeams) => {
      this.myTeams = myTeams
      this.events.unsubscribe("data")
    })

  }

  async ionViewDidLoad() {
    this.customer = this.sto.customer
    if (this.sto.customer.memberId) {
      this.myTeams = await this.myTeamService.teamList()
      if (this.sto.member) {
        this.member = this.sto.member
      }
    }
  }
  async outTeam() {
    await this.myTeamService.exitTeam(this.customer.id)
    this.http.createMessage("success", "退出团队成功")
    this.comm.updateSto()
    this.navCtrl.pop()
  }
  async editNickname(customer) {
    if (this.member.customerId == this.customer.id) {
      this.selectCustomer = customer
      this.navCtrl.push(NickNamePage, this.selectCustomer)

    } else {
      this.http.createMessage("error", "暂无权限，请联系掌柜")
    }

  }
  goInvita() {

    this.navCtrl.push(InvitationPage, { title: "收到的邀请" })
  }
  goRevice() {

    this.navCtrl.push(InvitationPage, { title: "发出的邀请" })
  }
  addPeople() {
    this.navCtrl.push(AddTeamerPage)
  }

}



