import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { AccRecvPay } from '../../share_platform/market/entity';
import { WalletExpendDetailPage } from '../wallet-expend-detail/wallet-expend-detail';
import { appMemberAccountService } from '../../app/share/service/app_member_account.service';
import { StorageService } from '../../app/share/service/storage.service';

/**
 * Generated class for the WalletExpendPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-wallet-expend',
  templateUrl: 'wallet-expend.html',
})
export class WalletExpendPage {
  startDt: any = new Date().toISOString();
  endDt: any = new Date().toISOString();
  exList: AccRecvPay[]
  add = 1
  reduc = -1
  myDate
  constructor(public navCtrl: NavController, public navParams: NavParams,
    public memS: appMemberAccountService,
    public sto: StorageService
  ) {
  }
  goDetail(data) {
    this.navCtrl.push(WalletExpendDetailPage, data)
  }
  async ionViewDidLoad() {
    console.log(this.startDt)
    let result = await this.memS.recvpayListBymemberId(this.sto.member.id)

    this.exList = result.accRecvPayList

  }
  public event = {
    month: '1990-02-19',
    timeStarts: '1990-02-19',
    timeEnds: '1990-02-20'
  }
  curDate = new Date();
  today() {
    this.startDt = new Date().toISOString();
    this.endDt = new Date().toISOString();
  }
  yesterday() {

    let result = new Date(this.curDate.getTime() - 24 * 60 * 60 * 1000)

    this.startDt = new Date(result).toISOString();
    this.endDt = new Date().toISOString();
    console.log(this.startDt)
  }
  threeDay() {
    let result = new Date(this.curDate.getTime() - 24 * 3 * 60 * 60 * 1000)
    this.startDt = new Date(result).toISOString();
    this.endDt = new Date().toISOString();
    console.log(this.startDt)
  }
  date = new Date();
  monthOne() {
    let result = new Date(this.date.getFullYear(), this.date.getMonth(), 2);
    let lastDay = new Date(this.date.getFullYear(), this.date.getMonth() + 1, 1);

    this.startDt = new Date(result).toISOString();
    this.endDt = new Date(lastDay).toISOString();
  }

}



