import { Component } from '@angular/core';
import { NavController, NavParams, AlertController, Keyboard } from 'ionic-angular';
import { ForgetPassword1Page } from "../forget-password1/forget-password1";
import { Register1Page } from "../register1/register1";
// import { HomePage } from "../home/home"
import { StorageService } from '../../../app/share/service/storage.service';
import { CustomerMainPage } from '../../main-home/customer-main/customer-main';
import { NativePageTransitions, NativeTransitionOptions } from '@ionic-native/native-page-transitions';
import { appCommonService } from '../../../app/share/service/app_common.service';
import { appCustomerService } from '../../../app/share/service/app_customer.service';
import { Market } from '../../../share_platform/framework/entity';
import { appMemberAccountService } from '../../../app/share/service/app_member_account.service';
import { Customer } from '../../../share_platform/market/entity';
import { appMessageService } from '../../../app/share/service/app_message.service';

/**   
 * Generated class for the SignInPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */
// @IonicPage()




@Component({
  selector: 'page-sign-in',
  templateUrl: 'sign-in.html',
})

export class SignInPage {
  //是否记住密码
  remember: boolean = false;
  user;
  mktId: number = 1;
  mobi: string = '15172480645';
  password: string = '123456';
  marketList: Market[] = [];
  market: Market
  customer: Customer
  redPassword: boolean = false;
  redPhone: boolean = true;
  constructor(public navCtrl: NavController, public navParams: NavParams,
    public customerService: appCustomerService,
    public storage: StorageService,
    private nativePageTransitions: NativePageTransitions,
    public alert: AlertController,
    public comm: appCommonService,
    public meService: appMemberAccountService,
    public cusService: appCustomerService,
    private keyboard: Keyboard
  ) {

    this.market = this.storage.market
    this.mktId = this.market.mktId
    console.log(this.market.mktId)
    let local = JSON.parse(localStorage.getItem('user'));
    if (!!local) {
      if (local.mktId == this.mktId) {
        this.mobi = local.mobi;
        this.password = local.password;
      }
    }

    console.log(this.mktId)

  }


  async ionViewDidLoad() {
    // this.marketList = await this.comm.marketList();

    console.log('ionViewDidLoad SignInPage---');
  }




  ionViewWillLeave() {
    let options: NativeTransitionOptions = {
      direction: 'right',
      duration: 500,
      slowdownfactor: 3,
    };
    this.nativePageTransitions.flip(options)
  }

  fogotPassword() {

    //   this.navCtrl.push(ForgetPassword1Page, null, { animation: 'wp-transition' });
    this.navCtrl.push(ForgetPassword1Page);
  }
  register() {
    this.navCtrl.push(Register1Page);
  }
  async signIn() {
    console.log(this.mktId, this.mobi, this.password)
    if (!this.mobi || !this.mktId) {
      return this.customerService.http.createMessage('error', '信息不完善');
    }

    let result = await this.customerService.login(this.mobi, this.password, this.mktId);

    if (result) {
      this.customer = result.customer
      this.storage.customer = this.customer
      this.storage.token = result.token

      this.navCtrl.push(CustomerMainPage);

      this.storage.customer = this.customer
      if (this.customer.memberId) {
        this.storage.member = await this.meService.memberDetail()
      }

    } else {
      // this.navCtrl.push(CustomerMainPage); 
      return this.customerService.http.createMessage('error', '账户或密码错误');
    }

  }
  fous() {
    this.keyboard.hideFormAccessoryBar(true)
    this.keyboard.didShow;

  }

}



