import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { RepaymentPage } from '../repayment/repayment';

/**
 * Generated class for the RepaymentListPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-repayment-list',
  templateUrl: 'repayment-list.html',
})
export class RepaymentListPage {

  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad RepaymentListPage');
  }
  async goList() {
    this.navCtrl.push(RepaymentPage)
  }

}



