import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Events } from 'ionic-angular';
import { appProductOrderService } from '../../app/share/service/app_ProductOrder.service';
import { Product } from '../../share_platform/market/entity';

/**
 * Generated class for the ProductDetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * 
 * Ionic pages and navigation.
 */

// @IonicPage()
@Component({
  selector: 'page-product-detail',
  templateUrl: 'product-detail.html',
})
export class ProductDetailPage {

  index
  products
  product: Product

  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public event: Events,
    public proService: appProductOrderService
  ) {

    this.index = this.navParams.get('index');
    this.products = JSON.parse(localStorage.getItem('productList'));
    this.product = this.products[this.index]
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ProductDetailPage');
  }
  async save() {
    await this.proService.productUpdate(this.product)
    // let product = { name: this.product.name, number: this.product.number, price: this.product.price, sum: ~~this.product.number * ~~this.product.price }
    // this.products[this.index] = this.product
    // localStorage.setItem('productList', JSON.stringify(this.products))

    this.navCtrl.pop().then(() =>
      this.event.publish('commodity', this.products)
    )
  }

}



