import { MyHttpService } from "./http.service";
import { StorageService } from "./storage.service";
import { Injectable } from "@angular/core";
import { AccRecvPay, MemberAccount, Member, Customer, ContactInvite } from "../../../share_platform/market/entity";

@Injectable()
export class appMemberAccountService {
    private api = {

        /**
    * 提交会员实名认证信息
    *   post
    * body:MemberRealnameAuth
    */
        memberRealnameAuthCreate: '/app/member/memberRealnameAuth/create',

        /**
         * get 
         * ?customerId&mktId
         * return {customer,status:}
         */
        memberDetail: '/app/member/memberDetail',
        /**
         * get
         * ?memberId&mktId
         * return {account}
         */
        accountDetail: '/app/member/accountDetail',

        /**
         * 转账
       * post
       * body:AccoPayRec
       */
        transferAccount: '/app/member/transferAccount',
        /**
         * 获取收钱信息
         * 1. 链接转为二维码形式等待对方扫
         * 2. 扫后返回对应的api返回收钱参数,如accountName,金额
         * 
         * get
         * ?mktId&accountId&amt
         * return {account,amt}
         */
        recvMoneyInfo: '/app/member/order/recive-money-info',
        /**
         * 此时用户扫了收钱二维码,将recvMoneyInfo返回的参数作为请求参数参数
         * 
         * post
         * body:AccPayRecv 
         * 
         */
        payMoneyInfo: '/app/member/order/pay-money',
        /**实名认证*/
        memberRealnameAuth: '/app/member/memberRealnameAuth/create',
        /**会员绑卡 */
        relationCard: '/app/customer/relationCard',
        /**会员支付密码验证  get memberId payPassword*/
        confirePayPassword: '/app/member/confirePayPassword',
        /**会员更改支付密码  get memberId oldPayPassword  newPayPasswoord */
        modifyPayPassword: "/app/member/modifyPayPassword",
        /**解绑会员卡  get cardNo*/
        relieveCard: "/app/customer/relieveCard",
        /**转账---根据手机号或姓名搜索老板  get keyWord */
        searchMember: "/app/member/searchMember",

        /**好友 邀请别人成为我的好友  get memberId friendId  content*/
        memberFriendInvi: "/app/member/friendInvi",

        /**好友 同意邀请 拒绝  get memberId status*/
        Inviprocess: "/app/member/Inviprocess",

        /**好友 发出的邀请  收到的邀请 get memberId type */
        memberInviList: "/app/member/InviList",


        /**好友  好友列表 get  memberId*/
        memberFriends: '/app/member/friendLists',
        /**收支列表 get memberId */
        recvpayListBymemberId: "/app/member/recvpayListBymemberId",

    }
    async recvpayListBymemberId(memberId) {
        let result = await this.http.Get(this.api.recvpayListBymemberId, { params: { memberId } })
        return result
    }
    async memberInviList(memberId: number, type: string) {
        let result = await this.http.Get(this.api.memberInviList, { params: { memberId, type } })
        return result
    }

    async Inviprocess(memInvi: ContactInvite) {
        let result = await this.http.Post(this.api.Inviprocess, memInvi)
        return result
    }
    async memberFriendInvi(memberId: number, friendId: number, content: string) {
        let result = await this.http.Get(this.api.memberFriendInvi, { params: { memberId, friendId, content } })
        return result
    }
    async searchMember(keyWord: string) {
        let result = await this.http.Get(this.api.searchMember, { params: { keyWord } })
        return result
    }
    async relieveCard(cardNo: number) {
        let result = await this.http.Get(this.api.relieveCard, { params: { cardNo } })
        return result
    }
    async modifyPayPassword(memberId: number, newPayPasswoord: string) {

        let result = await this.http.Get(this.api.modifyPayPassword, { params: { memberId, newPayPasswoord } })
        return result
    }
    async confirePayPassword(memberId: number, payPassword: string) {
        let result = await this.http.Get(this.api.confirePayPassword, { params: { memberId, payPassword } })
        return result
    }
    async relationCard(cus: Customer) {
        let result = await this.http.Post(this.api.relationCard, cus)
        return result
    }
    async memberRealnameAuthCreate(member: Member) {
        member.mktId = this.storage.customer.mktId
        let result = await this.http.Post(this.api.memberRealnameAuthCreate, member)
        return result.member
    }

    async accountDetail(memberId?: number, mktId?: number) {
        mktId = this.storage.customer.mktId
        memberId = this.storage.customer.memberId
        let result = await this.http.Get(this.api.accountDetail, { params: { memberId, mktId } })
        return result
    }
    async memberFriends(memberId: number) {
        let result = await this.http.Get(this.api.memberFriends, { params: { memberId } })
        return result.member
    }
    async transferAccount(memberId: number, tomemberId: number, transferAmount: number) {
        let result = await this.http.Get(this.api.transferAccount, {
            params: {
                memberId, tomemberId, transferAmount
            }
        })
        return result
    }
    async recvMoneyInfo(mktId: number, accountId: number, amt: number) {
        let result = await this.http.Get(this.api.recvMoneyInfo, { params: { mktId, accountId, amt } })
        return result
    }
    async payMoneyInfo(accPayRecv: AccRecvPay) {
        let result = await this.http.Post(this.api.payMoneyInfo, accPayRecv)
        return result
    }

    // async memberRealnameAuth(memberRealnameAuth:MemberRealnameAuth , authcode: string) {
    //     memberRealnameAuth.mktId = this.storage.customer.mktId;
    //     let res = await this.http.Post(this.api.memberRealnameAuth, memberRealnameAuth, { params: { authcode } });
    //     return res;
    // }
    async  memberDetail(memberId?: number, mktId?: number) {
        mktId = this.storage.customer.mktId
        memberId = this.storage.customer.memberId
        let result = await this.http.Get(this.api.memberDetail, { params: { memberId, mktId } });
        if (result) {
            return result.member
        }
    }
    constructor(public http: MyHttpService, public storage: StorageService) {

    }
}


