import { Injectable } from "@angular/core";
import { MyHttpService } from "./http.service";
import { Order, Product, ProdCatalog, Customer, orderPayRq } from "../../../share_platform/market/entity";
import { StorageService } from "./storage.service";
import { ProductStatusEnum, PayTypeEnum, queryOrderStatus } from "../../../share_platform/market/enum";


import { Observable } from "rxjs/Observable";
import { HttpClient } from "@angular/common/http";

@Injectable()
export class appProductOrderService {
    private api = {
        /**
         * 创建订单
         * post
         * body:order
         */
        orderCreate: '/app/order/orderCreate',
        /**
         * post
         * body:TempOrder
         */
        orderUpload: '/app/order/upload',
        orderDowload: '/app/order/download',
        /**
         * 在线支付订单
         * get
         * ?orderId&memberId&mktId
         */
        onlinePayOrder: '/app/order/online-pay-order',
        /**
         * get
         * 推送订单给指定好友
         * ?orderId&memberId&fromMemberId
         */
        sendOrderToMember: '/app/order/send-order-to-member',
        /**
         * 订单作废
         * get
         * ?orderId&memberId&mktId
         */
        orderDisabled: '/app/order/disabled',
        /**
         * 订单详情
         * 
         * ?orderId&mktId
         * return {order}
         */
        orderDetail: '/app/order/detail',
        /**
         * get
         * ?mktId&memberId
         */
        productList: '/app/order/list',
        /**
         * 列出所有市场分类指定parentId的分类
         * get
         * ?mktId&parentId
         */
        productCatList: '/app/order/productCat/list',
        /**
         * 赊销申请
         * get
         * ?orderId&fromMemberId&toMebmerId
         */
        debtRequest: '/app/order/debt-request',
        /**
         * 赊账同意
         * get
         * ?orderId&memberId&mktId
         * 
         */
        debtAgree: '/app/order/debt-agree',
        /**
         * 拒绝赊账
         * ?orderId&memberId&mktId
         */
        debtRefuse: '/app/order/debt-refuse',
        /**
                * 创建产品 post body:Product
                */
        productCreate: "/app/order/product/create",
        /**
         * 更新产品 post body:Product
         */
        productUpdate: "/app/order/product/update",
        /**
         * 删除产品 get ?mktId&productId
         */
        productDel: "/app/order/product/del",
        /**
                 * 交易区列表 get ?mktId
                 */
        txnList: "/app/order/txn/list",

        /**
                 * get ?mktId&memberId&status  根据不同状态刷新列表  使用中 停用
                 */
        productListByStatus: "/app/order/product/list-by-status",
        /**
        * get ?mktId&memberId&keyword  根据关键字搜索
        */
        productListByKeyword: "/app/order/product/list-by-keyword",

        /**
         * 
         */
        writeBuyerInfo: "/app/order/writeBuyerInfo",
        /**
        * 交易分区
        */
        list: "/app/order/txn/list",
        /**
         * 上传订单
         */
        upload: "/app/order/upload",

        /**
        * 订单下载列表
        */
        downloadList: '/app/order/download/list',
        /**
         * 订单下载列表
         */
        download: '/app/order/tempOrderQuery',


        /**
          *  查询买家信用
          */
        qryBuyerCredit: '/app/sellOnCredit/qryBuyerCredit',
        /**
          *  是否同意赊销
          */
        changeStatus: '/app/order/changeStatus',
        /**
         * 查询订单状态
         */
        queryOrderStatus: '/app/order/queryOrderStatus',

        /**
         * 买家拿订单类型
         */
        exeQCode: '/app/order/exeQCode',
        /**
         * 查询订单详情
         */
        getOrderDetail: '/app/order/getOrderDetail',
        /**
         * 支付
         */
        orderPay: '/app/order/orderPay',

        /**
          * 赊销
          */
        sellOnCredit: '/app/sellOnCredit/orderPay'
    }

    async sellOnCredit(orderPayRq: orderPayRq) {
        let result = await this.http.Post(this.api.sellOnCredit, orderPayRq)
        return result
    }

    async orderPay(orderPayRq: orderPayRq) {
        let result = await this.http.Post(this.api.orderPay, orderPayRq)
        return result
    }



    async getOrderDetail(orderNo: string) {
        let result = await this.http.Get(this.api.getOrderDetail, { params: { orderNo } })
        return result
    }

    // getOrderDetail(orderNo: string) {
    //     return this.httpClient.get(this.api.getOrderDetail, { params: { orderNo } }).subscribe(rtn => { return rtn })

    // }


    /**
      *  查询买家信用
      */
    async exeQCode(base64Str: string) {
        let customerId = this.sto.customer.id;
        let result = await this.http.Get(this.api.exeQCode, { params: { base64Str, customerId } })
        return result
    }


    async qryBuyerCredit(orderNo: string) {
        let mktId = this.sto.market.mktId;
        let result = await this.http.Get(this.api.qryBuyerCredit, { params: { orderNo, mktId } })
        return result
    }

    /**
      * 查询订单状态
      */
    async querOrderStatus(orderNo: string) {
        let result = await this.http.Get(this.api.queryOrderStatus, { params: { orderNo } })
        return result
    }

    /**
      *  是否同意赊销
      */
    async changeStatus(orderNo: string, status: queryOrderStatus) {
        let result = await this.http.Get(this.api.changeStatus, { params: { orderNo, status } })
        return result
    }

    async download(orderNo: string) {
        let mktId = this.sto.market.mktId;
        let result = await this.http.Get(this.api.downloadList, { params: { mktId, orderNo } })
        return result
    }

    async downloadList() {
        let mktId = this.sto.market.mktId;
        let memberId = this.sto.member.id;
        let result = await this.http.Get(this.api.download, { params: { mktId, memberId } })
        return result
    }

    async upload(order: Order) {
        let result = await this.http.Post(this.api.upload, order)
        return result
    }


    async list() {
        let mktId = this.sto.market.mktId;
        let result = await this.http.Get(this.api.list, { params: { mktId } })
        return result

    }

    async writeBuyerInfo(orderNo: string, businessType: string, memberId?: number) {
        memberId = this.sto.customer.memberId
        let result = await this.http.Get(this.api.writeBuyerInfo, { params: { businessType, memberId, orderNo } })
        return result

    }


    async productListByKeyword(keyword: string, mktId?: number, memberId?: number, ) {
        mktId = this.sto.customer.mktId
        memberId = this.sto.customer.memberId

        let result = await this.http.Get(this.api.productListByKeyword, { params: { keyword, mktId, memberId } })
        return result.products

    }
    async  productListByStatus(status: ProductStatusEnum, mktId?: number, memberId?: number, ) {
        mktId = this.sto.customer.mktId
        memberId = this.sto.customer.memberId

        let result = await this.http.Get(this.api.productListByStatus, { params: { status, mktId, memberId } })
        return result.products
    }

    async txnList(mktId?: number) {
        mktId = this.sto.customer.mktId
        let result = await this.http.Get(this.api.txnList, { params: { mktId } })
        return result.areas
    }
    async productDel(productId: number, mktId?: number, ) {
        mktId = this.sto.customer.mktId
        let result = await this.http.Get(this.api.productDel, { params: { productId, mktId } })
        return result
    }
    async productUpdate(product: Product) {
        let result = await this.http.Post(this.api.productUpdate, product)
        return result;
    }
    async productCreate(product: Product) {
        product.mktId = this.sto.customer.mktId
        let result = await this.http.Post(this.api.productCreate, product)
        return result;
    }
    async orderCreate(order: Order) {
        let result = await this.http.Post(this.api.orderCreate, order)
        return result
    }
    async orderUpload(tempOrder: Order) {
        let result = await this.http.Post(this.api.orderUpload, tempOrder)
        return result
    }
    async orderDowload(tempOrder: Order) {
        let result = await this.http.Post(this.api.orderDowload, tempOrder)
        return result
    }
    async onlinePayOrder(orderId: number, memberId: number, mktId: number) {
        let result = await this.http.Get(this.api.onlinePayOrder, { params: { orderId, memberId, mktId } })
        return result
    }
    async sendOrderToMember(orderId: number, memberId: number, fromMemberId: number) {
        let result = await this.http.Get(this.api.sendOrderToMember, { params: { orderId, memberId, fromMemberId } })
        return result
    }
    async orderDisabled(orderId: number, memberId: number, mktId: number) {
        let result = await this.http.Get(this.api.orderDisabled, { params: { orderId, memberId, mktId } })
        return result
    }
    async orderDetail(orderId: number, mktId: number) {
        let result = await this.http.Get(this.api.orderDetail, { params: { orderId, mktId } })
        return result.order
    }
    async productList(mktId?: number, memberId?: number): Promise<Product[]> {
        mktId = this.sto.customer.mktId,
            memberId = this.sto.customer.memberId
        let result = await this.http.Get(this.api.productList, { params: { mktId, memberId } })
        return result.products
    }

    async productCatList(txnId: number, parentId?: number, mktId?: number, ): Promise<ProdCatalog[]> {
        mktId = this.sto.customer.mktId
        if (!parentId) {
            parentId = 0;
            let result = await this.http.Get(this.api.productCatList, { params: { txnId, parentId, mktId } })
            return result.productCatas
        } else {
            let result = await this.http.Get(this.api.productCatList, { params: { txnId, parentId, mktId } })
            return result.productCatas
        }
    }
    async debtRequest(orderId: number, fromMemberId: number, toMebmerId: number) {
        let result = await this.http.Get(this.api.debtRequest, { params: { orderId, fromMemberId, toMebmerId } })
        return result;
    }
    async debtAgree(orderId: number, memberId: number, mktId: number) {
        let result = await this.http.Get(this.api.debtAgree, { params: { orderId, memberId, mktId } })
        return result
    }
    async debtRefuse(orderId: number, memberId: number, mktId: number) {
        let result = await this.http.Get(this.api.debtRefuse, { params: { orderId, memberId, mktId } })
        return result
    }
    constructor(public http: MyHttpService, public sto: StorageService, public httpClient: HttpClient) { }
}


