import { Component } from '@angular/core';
import { Platform, AlertController, Events } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { JPush } from 'ionic3-jpush';
import { Observable } from "RxJS/Rx";

import { HomePage } from "../pages/main-home/home/home";
import { MarketPage } from '../pages/market/market';
import { OpenBillPage } from '../pages/open-bill/open-bill';
import { ProductAddPage } from "../pages/product-add/product-add"
// import { FootBarPage } from "../pages/foot-bar/foot-bar"

import { SignInPage } from "../pages/signin/sign-in/sign-in";
import { MktlistPage } from '../pages/mktlist/mktlist';
import { Device } from "@ionic-native/device";
import { ModalController, NavParams } from 'ionic-angular';
import { NotificationPage } from '../pages/notification/notification';
import { WeighSetupPage } from '../pages/weigh-setup/weigh-setup';
import { BLE } from '@ionic-native/ble';
import { ChangeDetectorRef } from '@angular/core';
import { QrCodePage } from '../pages/qr-code/qr-code';
import { OrderPayPage } from "../pages/order-pay/order-pay"
import { OpeningWallet3Page } from '../pages/opening-wallet3/opening-wallet3';
import { OrderPayEndPage } from '../pages/order-pay-end/order-pay-end';
import { OrderPayReciveEndPage } from '../pages/order-pay-recive-end/order-pay-recive-end';
import { buffer } from 'RxJS/operator/buffer';
import { CommonService } from './share/service/common.service';
import { EntityEnum } from '../share_platform/framework/enum';





@Component({
  templateUrl: 'app.html'
})

export class MyApp {
  rootPage: any = MktlistPage;
  devices: Device[] = [];
  devicePlatform: string;
  sequence: number = 0;
  weight = 0 //蓝牙秤重量
  blueData

  constructor(platform: Platform,
    statusBar: StatusBar,
    splashScreen: SplashScreen,
    public jpush: JPush,
    public event: Events,
    public alert: AlertController,
    public modelC: ModalController,
    public ble: BLE,
    public common: CommonService
  ) {
    platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      statusBar.styleDefault();
      splashScreen.hide();
      // platform.backButton.

    });


    // this.modelC.create(NotificationPage, { userId: 8675309 }).present();
    //点击通知进入应用程序时会触发的事件，点击通知进入程序
    this.jpush.openNotification().subscribe((msg) => {
      this.modelC.create(NotificationPage, msg).present();
    })


    //收到通知时会触发该事件，收到通知
    this.jpush.receiveNotification().subscribe((msg) => {
      this.alertCtrl(msg)
    })

    //监听自定义消息通知
    this.jpush.receiveMessage().subscribe((msg) => {
      this.alertCtrl(msg)
    })
    this.init();


    //蓝牙监听
    this.event.subscribe('', device => {
      let status
      this.ble.isConnected(device.id).then(() => {
        status = setInterval(() => {
          this.ble.startNotification(device.id, '0000ffe0-0000-1000-8000-00805f9b34fb', '0000ffe1-0000-1000-8000-00805f9b34fb').subscribe(buffer => {
            this.blueData = String.fromCharCode.apply(null, new Uint8Array(buffer)).split('').reverse().join('').replace('=', '');
          })
        }, 1000)
      }),
        () => {
          this.blueData = '未连接';
          clearInterval(status);
        }
    })

  }



  init() {
    this.jpush.init();//初始化 开启极光推送
    //提醒开启极光推送
    this.jpush.getUserNotificationSettings().then((res) => {
      if (res == 0) {
        // 系统设置中已关闭应用推送。
      } else if (res > 0) {
        // 系统设置中打开了应用推送。
      }
    })

  }




  alertCtrl(msg) {
    this.alert.create({
      title: "推送内容",
      message: JSON.stringify(msg),
      buttons: [
        {
          text: '取消',
          handler: () => {
          }
        },
        {
          text: '确认',
          handler: () => {
          }
        }
      ]
    }).present();
  }




  async  getRegId() {
    this.regId = await this.jpush.getRegistrationID();

  }
  regId
}




