import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { RegisterDetailPage } from "../register-detail/register-detail"
import { appCommonService } from '../../../app/share/service/app_common.service';
import { Market } from '../../../share_platform/framework/entity';
import { VerifyType } from '../../../share_platform/framework/enum';
import { StorageService } from '../../../app/share/service/storage.service';
import { MyHttpService } from '../../../app/share/service/http.service';
import { CommonService } from '../../../app/share/service/common.service';
/**
 * Generated class for the Register1Page page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

// @IonicPage()
@Component({
  selector: 'page-register1',
  templateUrl: 'register1.html',
})
export class Register1Page {
  mktId: number;
  looptime: number = 60;
  marketList: Market[] = []
  phone: string = "";
  authcode: string = "";
  sendBool: boolean = true;
  market: Market
  verifyType = VerifyType.reg

  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public customer: appCommonService,
    public sto: StorageService,
    public http: MyHttpService,
    public comm: CommonService
  ) {
    this.market = this.sto.market
    this.mktId = this.market.mktId
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad Register1Page');
    // this.customer.marketList().then(list => this.marketList = list);

  }
  goNext() {
    this.checkAuthcode();
  }
  async  sendAuthcode() {
    let phoneReg: RegExp = /^((1[3,5,8][0-9])|(14[5,7])|(17[0,6,7,8])|(19[7]))\d{8}$/;
    if (!this.phone) {
      this.http.createMessage("warning", "请输入手机号")
    } else {
      if (!(phoneReg.test(this.phone))) {
        this.customer.http.createMessage("error", "请输入正确手机号")
      } else {

        let result = await this.customer.authcode(this.mktId, this.phone, this.verifyType);
        if (result) {
          this.looptime = 60;
          let timmer = setInterval(() => {
            this.looptime -= 1;
            if (this.looptime <= 0) {
              this.sendBool = true
              clearInterval(timmer);
            }

          }, 1000);
        } else {
          this.sendBool = true
        }
      }
    }

  }

  async checkAuthcode() {
    if (!this.authcode || !this.phone) {
      this.http.createMessage("warning", "请完善信息")

    } else {
      let result = await this.customer.checkAuthcode(this.phone, this.authcode, this.mktId, this.verifyType)
      if (result) {
        this.navCtrl.push(RegisterDetailPage, { authcode: this.authcode, phone: this.phone, mktId: this.mktId })
      }
    }

  }
  async getMemberDetail() {
    //  this.customer.getMemberDetail()
  }

}



