import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { CustomerPage } from "../customer/customer"
import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';
import { CommonService } from '../../app/share/service/common.service';
import { EntityEnum } from '../../share_platform/framework/enum';
import { Member } from '../../share_platform/market/entity';
import { StorageService } from '../../app/share/service/storage.service';
/**
 * Generated class for the StatisticalProductPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

// @IonicPage()
@Component({
  selector: 'page-statistical-product',
  templateUrl: 'statistical-product.html',
})
export class StatisticalProductPage {
  startDt: any = new Date().toISOString();
  endDt: any = new Date().toISOString();
  member: Member
  constructor(public navCtrl: NavController, public navParams: NavParams,
    public commS: CommonService,
    public sto: StorageService

  ) {
  }

  async ionViewDidLoad() {
    debugger
    let result = await this.commS.singleTableQuery(EntityEnum.Product, { memberId: this.sto.member.id }, { pageIndex: 1, pageSize: 10 })
    return result

  }
  public event = {
    month: '1990-02-19',
    timeStarts: '07:43',
    timeEnds: '1990-02-20'
  }
  goCustomerPage() {
    this.navCtrl.push(CustomerPage)
  }
}



