import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { SignInPage } from "../sign-in/sign-in"
import { appCustomerService } from '../../../app/share/service/app_customer.service';
import { Customer } from '../../../share_platform/market/entity';
import { StorageService } from '../../../app/share/service/storage.service';
import { MyHttpService } from '../../../app/share/service/http.service';
/**
 * Generated class for the ForgetPassword2Page page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

// @IonicPage()

@Component({
  selector: 'page-forget-password2',
  templateUrl: 'forget-password2.html',
})
export class ForgetPassword2Page {
  modifyCustomer: Customer = {
    mobi: '',
    password: ""
  }

  repeatPassword: string = '';
  authcode: string = "";

  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public customer: appCustomerService,
    public sto: StorageService,
    public http: MyHttpService
  ) {
    this.modifyCustomer.mobi = this.navParams.data.phone;
    this.modifyCustomer.mktId = this.navParams.data.mktId;
    this.authcode = this.navParams.data.authcode;

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ForgetPassword2Page');
  }
  async goNext() {
    if (this.modifyCustomer.password == this.repeatPassword) {
      this.modifyCustomer.mktId = this.sto.market.mktId
      let result = await this.customer.forgotPassword(this.modifyCustomer, this.authcode);
      if (result) {
        this.http.createMessage("success", "修改成功")
        this.navCtrl.setRoot(SignInPage);

      }
    } else {
      this.customer.http.createMessage('error', '密码不一致');
    }

  }
}



