import { Injectable } from "@angular/core";
import { MyHttpService } from "./http.service";
import { StorageService } from "./storage.service";

@Injectable()
export class appSellOnCreditService {
    private api = {
        /*** 查询赊销记录列表 post queryParameter */
        accountDebtList: "/app/sellOnCredit/accountDebtList",
        /**查询还款记录列表 post queryParameter  */
        accountRepayList: "/app/sellOnCredit/accountRepayList",
        /**get id flag */
        confirmRepay: "/app/sellOnCredit/confirmRepay",


        /**post accountRepayRq  */
        offlineRepayApply: "/app/sellOnCredit/offlineRepayApply",
        /**get payPassword id  */

        offlineRepayConfire: "/app/sellOnCredit/offlineRepayConfire",
        /**post accountRepayRq  */

        onlineRepay: "/app/sellOnCredit/onlineRepay",

        /**赊销订单支付 post orderPayRq  */
        orderPay: "/app/sellOnCredit/orderPay",


        /**
         *  查询买家信用
         */
        qryBuyerCredit: '/app/sellOnCredit/qryBuyerCredit',


    }
    async accountDebtList(queryParameter) {
        let result = await this.http.Post(this.api.accountDebtList, queryParameter)
        return result
    }
    async accountRepayList(queryParameter) {
        let result = await this.http.Post(this.api.accountRepayList, queryParameter)
        return result
    }
    async confirmRepay(id, flag) {
        let result = await this.http.Get(this.api.confirmRepay, { params: { id, flag } })
        return result
    }
    async offlineRepayApply(accountRepayRq) {
        let result = await this.http.Post(this.api.offlineRepayApply, accountRepayRq)
        return result
    }
    async offlineRepayConfire(id, payPassword) {
        let result = await this.http.Post(this.api.offlineRepayConfire, { params: { id, payPassword } })
        return result
    }
    async onlineRepay(accountRepayRq) {
        let result = await this.http.Post(this.api.onlineRepay, accountRepayRq)
        return result
    }
    /**赊销订单支付 */
    async orderPay(orderPayRq) {
        let result = await this.http.Post(this.api.orderPay, orderPayRq)
        return result
    }
    /**查询买家信息 */
    async qryBuyerCredit(orderNo: string) {
        let mktId = this.sto.market.mktId;
        let result = await this.http.Get(this.api.qryBuyerCredit, { params: { orderNo, mktId } })
        return result
    }
    constructor(public http: MyHttpService, public sto: StorageService) { }
}






