import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ResetPayPassword1Page } from '../reset-pay-password1/reset-pay-password1';
import { ResetPayPassword2Page } from '../reset-pay-password2/reset-pay-password2';

/**
 * Generated class for the ResetPayPasswordPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-reset-pay-password',
  templateUrl: 'reset-pay-password.html',
})
export class ResetPayPasswordPage {

  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ResetPayPasswordPage');
  }
  forget() {
    this.navCtrl.push(ResetPayPassword2Page)
  }
  remember() {
    this.navCtrl.push(ResetPayPassword1Page)
  }
}



