import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Customer, MemberInvite } from '../../share_platform/market/entity';
import { appTeamService } from '../../app/share/service/app_team.service';
import { StorageService } from '../../app/share/service/storage.service';
import { appCustomerService } from '../../app/share/service/app_customer.service';
import { MyHttpService } from '../../app/share/service/http.service';

/**
 * Generated class for the TeamAddDetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

// @IonicPage()
@Component({
  selector: 'page-team-add-detail',
  templateUrl: 'team-add-detail.html',
})
export class TeamAddDetailPage {
  newCus: Customer
  newMemberInvite: MemberInvite = {

  }
  constructor(public navCtrl: NavController, public navParams: NavParams,
    public teamService: appTeamService,
    public sto: StorageService,
    public cusService: appCustomerService,
    public http: MyHttpService
  ) {
    this.newCus = this.navParams.data

  }

  ionViewDidLoad() {



  }
  async ok() {
    this.newMemberInvite.mktId = this.sto.member.mktId
    this.newMemberInvite.sendCustId = this.sto.member.customerId
    this.newMemberInvite.recvMobi = this.newCus.mobi
    this.newMemberInvite.recvCustId = this.newCus.id
    this.newMemberInvite.sendCusName = this.sto.member.name
    this.newMemberInvite.recvCusName = this.newCus.name
    let result = await this.teamService.inviteCustomer(this.newMemberInvite)
    if (result) {
      this.http.createMessage("success", "邀请已发送")
      this.navCtrl.pop()
    }

  }
}



