import { Injectable } from "@angular/core";
import { MyHttpService } from "./http.service";
import { StorageService } from "./storage.service";

@Injectable()
export class appMessageService {
    private api = {
        /**
        * get
        * 该接口可以暂时不要
        */
        settingList: '/app/customer/message/setting-list',
        /**
         * 设置自己的消息
         * post
         * body: {newMsgSetting&customerId}
         */
        msgManage: '/app/customer/message/manage',
        /**
         * get
         * ?customerId&messageTypes
         */
        msgQuery: '/app/customer/message/query',
        msgChecked: '/app/customer/message/checked',

    }
    async msgChecked(messageId) {
        let result = await this.http.Get(this.api.msgChecked, { params: { messageId, customerId: this.stroge.customer.id } })
        return result;
    }

    async msgManage(newMsgSetting: string) {
        let result = await this.http.Post(this.api.msgManage, { newMsgSetting }, { params: { customerId: this.stroge.customer.id } })
        return result;
    }
    async msgQuery(messageTypes: string) {
        let result = await this.http.Get(this.api.msgQuery, { params: { customerId: this.stroge.customer.id, messageTypes } })
        return result.msgs;
    }
    constructor(public http: MyHttpService, public stroge: StorageService) { }
}


