import { Component } from '@angular/core';
import { NavController, NavParams, App } from 'ionic-angular';
import { Market } from '../../share_platform/framework/entity';
import { VerifyType } from '../../share_platform/framework/enum';
import { MyHttpService } from '../../app/share/service/http.service';
import { appCommonService } from '../../app/share/service/app_common.service';
import { StorageService } from '../../app/share/service/storage.service';
import { appCustomerService } from '../../app/share/service/app_customer.service';
import { MktlistPage } from '../mktlist/mktlist';

/**
 * Generated class for the ProductDPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

// @IonicPage()



@Component({
  selector: 'page-modify-new-phone',
  templateUrl: 'modify-new-phone.html',
})
export class ModifyNewPhonePage {
  mktId: number;
  looptime: number = 60;
  marketList: Market[] = []
  phone: string = "";
  authcode: string = "";
  sendBool: boolean = true;
  market: Market
  verifyType = VerifyType.reg
  constructor(public navCtrl: NavController, public navParams: NavParams,
    public http: MyHttpService,
    public comm: appCommonService,
    public sto: StorageService,
    public cus: appCustomerService,
    public app: App
  ) {
    this.mktId = this.sto.customer.mktId
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ProductDPage');
  }
  goNext() {
    this.checkAuthcode();
  }
  async  sendAuthcode() {
    let phoneReg: RegExp = /^((1[3,5,8][0-9])|(14[5,7])|(17[0,6,7,8])|(19[7]))\d{8}$/;
    if (!this.phone) {
      this.http.createMessage("warning", "请输入手机号")
    } else {
      if (!(phoneReg.test(this.phone))) {
        this.http.createMessage("error", "请输入正确手机号")
      } else {

        let result = await this.comm.authcode(this.mktId, this.phone, this.verifyType);
        if (result) {
          this.looptime = 60;
          let timmer = setInterval(() => {
            this.looptime -= 1;
            if (this.looptime <= 0) {
              this.sendBool = true
              clearInterval(timmer);
            }

          }, 1000);
        } else {
          this.sendBool = true
        }
      }
    }

  }

  async checkAuthcode() {
    if (!this.authcode || !this.phone) {
      this.http.createMessage("warning", "请完善信息")

    } else {
      let result = await this.comm.checkAuthcode(this.phone, this.authcode, this.mktId, this.verifyType)
      if (result) {
        let result = await this.cus.modifyPhone(this.sto.customer.mobi, this.phone, this.authcode, this.sto.customer.mktId, this.sto.customer.id)
        if (result) {
          this.http.createMessage("success", "修改成功，请重新登录")
          localStorage.clear()
          this.app.getRootNav().setRoot(MktlistPage)
        } else {
          this.http.createMessage("warning", "请更换手机号")
        }
      }
    }

  }

}



