import { Component } from '@angular/core';
import {
  // IonicPage,
  NavController, NavParams, App, AlertController, Events
} from 'ionic-angular';
import { HomePage } from '../home/home';
// import { OpenBillPage } from '../open-bill/open-bill';
import { PersonInfoPage } from '../../person-info/person-info';
import { WalletPage } from '../../wallet/wallet';
import { JPush } from 'ionic3-jpush';
import { appProductOrderService } from '../../../app/share/service/app_ProductOrder.service';
import { appCustomerService } from '../../../app/share/service/app_customer.service';
import { appMemberAccountService } from '../../../app/share/service/app_member_account.service';
import { Market } from '../../../share_platform/framework/entity';
import { Customer, Member } from '../../../share_platform/market/entity';
import { StorageService } from '../../../app/share/service/storage.service';
import { OpeningWallet1Page } from "../../opening-wallet1/opening-wallet1"
import { MemberStatusEnum, AccountStatusEnum } from '../../../share_platform/market/enum';
import { MyHttpService } from '../../../app/share/service/http.service';
import { FnParam, fn } from '@angular/compiler/src/output/output_ast';
import { OpenBillPage } from '../../open-bill/open-bill';

/**
 * Generated class for the CustomerMainPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

// @IonicPage()
@Component({
  selector: 'page-customer-main',
  templateUrl: 'customer-main.html',
})
export class CustomerMainPage {
  homePage = HomePage;
  redPacket;
  personInfo = PersonInfoPage

  market: Market;
  cus: Customer
  member: Member

  memStatus = AccountStatusEnum
  tab2 = this.stor.customer.memberId ? WalletPage : OpeningWallet1Page

  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public jpush: JPush,
    public alert: AlertController,
    public customerService: appCustomerService,
    public proService: appProductOrderService,
    public memService: appMemberAccountService,
    public stor: StorageService,
    public http: MyHttpService,
    public app: App,
    public events: Events
  ) {
    this.jpush.getRegistrationID().then((id) => {
      if (!!id) {
        this.customerService.updateRegistrationId(id);
        this.alertCtrl(id)
      } else {
        this.alertCtrl('推送功能打开失败')
      }
    })
    this.events.subscribe('data', (res) => {
      this.member = res

      this.events.unsubscribe('data');
    });


  }
  async ionViewCanEnter() {
    if (this.stor.customer.id) {

      this.cus = this.stor.customer
      if (this.stor.member) {
        this.member = this.stor.member
      }
    }

  }
  async ionViewDidLoad() {

  }



  async change() {

    this.cus = this.stor.customer
    if (this.cus.memberId) {
      this.tab2 = WalletPage
      this.member = await this.memService.memberDetail()
      if (this.member.status == this.memStatus.FROZEN) {
        this.http.createMessage("warning", "账户冻结，请联系管理员")
      } else
        if (this.member.status == this.memStatus.DISABLE) {
          this.http.createMessage("warning", "账户禁用，请联系管理员")
        }
        else if (this.member.status == this.memStatus.ENABLE) {
          this.app.getRootNav().setRoot(WalletPage)
        }
        else if (this.member.status == this.memStatus.CANCEL) {
          this.navCtrl.push(OpeningWallet1Page)
        }
    } else {
      this.tab2 = OpeningWallet1Page
    }
  }
  alertCtrl(msg) {
    this.alert.create({
      title: "推送内容",
      message: JSON.stringify(msg),
      buttons: [
        {
          text: '取消',
          handler: () => {
          }
        },
        {
          text: '确认',
          handler: () => {
          }
        }
      ]
    }).present();
  }





}



