import { Component } from '@angular/core';
import { NavController, Alert, Events, NavParams } from 'ionic-angular';

import { AlertController } from 'ionic-angular';
import { BLE } from '@ionic-native/ble';

/**
 * Generated class for the BlueListPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */








@Component({
  selector: 'page-blue-list',
  templateUrl: 'blue-list.html',
})
export class BlueListPage {


  devices = [];
  gettingDevices: Boolean;
  currentDevice: any;
  scan: boolean = true;
  loop: boolean = true;
  data = [];
  paramsDevice;

  address;

  constructor(private ble: BLE, private alertCtrl: AlertController, public navParams: NavParams, public event: Events) {
    this.paramsDevice = this.navParams.get('device');
    this.ble.enable()
    this.gettingDevices = true;


    this.ble.scan([], 20).subscribe(device => {
      this.devices.push(device);
      this.gettingDevices = false;
    })
  }





  selectDevice(device: any) {
    let alert = this.alertCtrl.create({
      title: '连接',
      message: '确定连接?',
      buttons: [
        {
          text: '取消',
          role: 'cancel',
          handler: () => {

          }
        },
        {
          text: '连接',
          handler: () => {
            if (this.paramsDevice == 'printer') {
              this.ble.connect(device.id).subscribe(() => {
                this.alert('连接成功');
                this.event.publish('printer', device);
              })
            } else if (this.paramsDevice == 'scales') {
              this.ble.connect(device.id).subscribe(() => {
                this.alert('连接成功');
                this.event.publish('scales', device);
              })
            }
          }
        },
      ]
    });
    alert.present();



  }

  stopScan(address: any) {
    this.scan = !this.scan
    let alert = this.alertCtrl.create({
      title: '停止扫描?',
      message: '确定停止扫描吗？',
      buttons: [
        {
          text: '取消',
          handler: () => {
          }
        },
        {
          text: '停止扫描',
          handler: () => {
            this.ble.stopScan().then(
              success => {
                this.gettingDevices = false;
              }
            )
          }


        }
      ]
    });
    alert.present();
  }



  alert(data) {
    let alert = this.alertCtrl.create({
      title: data,
      message: data,
      buttons: [
        {
          text: '确定',
          role: '确定',
        },
      ]
    });
    alert.present();
  }



}




