import { Component, ChangeDetectorRef } from '@angular/core';
import { IonicPage, NavController, NavParams, Keyboard } from 'ionic-angular';
import { SignInPage } from '../signin/sign-in/sign-in';
import { appCommonService } from '../../app/share/service/app_common.service';
import { Market, QueryObject } from '../../share_platform/framework/entity';
import { StorageService } from '../../app/share/service/storage.service';
import { CustomerMainPage } from "../main-home/customer-main/customer-main"
import { PriceUnitEnum } from '../../share_platform/market/enum';
import { CommonService } from '../../app/share/service/common.service';
import { EntityEnum } from '../../share_platform/framework/enum';
import { MyHttpService } from '../../app/share/service/http.service';
/**
 * Generated class for the MktlistPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-mktlist',
  templateUrl: 'mktlist.html',
})
export class MktlistPage {

  markets: Market[]
  selsetMkt: Market
  priceW = PriceUnitEnum
  b_mkt = EntityEnum.Market
  keyWord = ""
  indx = 1
  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public comm: appCommonService,
    public sto: StorageService,
    public comSer: CommonService,
    public http: MyHttpService

  ) {

  }









  async  ionViewDidLoad() {

    if (this.sto.customer) {
      this.navCtrl.push(CustomerMainPage)
    } else {

      let result = await this.comSer.singleTableQuery(this.b_mkt, {}, { pageIndex: 0, pageSize: 5 })
      if (result) {
        this.markets = result.rows
      } else {
        this.http.createMessage("warning", "")
      }


    }

  }
  async serchKeyWord($event) {

    let key_code = window.event ? $event.keyCode : $event.which;
    if (key_code == 13) {
      let result = await this.comSer.singleTableQuery(this.b_mkt, { mktName: { $like: this.keyWord } })
      this.markets = result.rows

    }


  }
  select(market) {
    this.selsetMkt = market
  }
  goSignIn() {

    this.sto.market = this.selsetMkt
    this.navCtrl.push(SignInPage, this.selsetMkt);
    if (!this.sto.priceW) {
      this.sto.priceW = this.priceW.JIN
    }
  }

  doRefresh(refresher) {
    console.log('Begin async operation', refresher);

    setTimeout(() => {
      console.log('Async operation has ended');
      // this.markets = ['市场1', '市场2', '市场3', '市场1', '市场2', '市场3', '市场1', '市场2', '市场3', '市场1', '市场2', '市场3'];
      refresher.complete();
    }, 2000);
  }

  doInfinite(infiniteScroll) {
    console.log('Begin async operation');

    setTimeout(() => {
      for (let i = 0; i < 10; i++) {
        // this.markets.push('市场n');
      }

      console.log('Async operation has ended');
      infiniteScroll.complete();
    }, 500);
  }


}



