import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { OpeningWallet3Page } from "../opening-wallet3/opening-wallet3";
import { StorageService } from '../../app/share/service/storage.service';
import { CommonService } from '../../app/share/service/common.service';
import { appCommonService } from '../../app/share/service/app_common.service';
import { Member } from '../../share_platform/market/entity';
import { VerifyType } from '../../share_platform/framework/enum';
import { MyHttpService } from '../../app/share/service/http.service';
/**
 * Generated class for the OpeningWallet2Page page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

// @IonicPage()
@Component({
  selector: 'page-opening-wallet2',
  templateUrl: 'opening-wallet2.html',
  // styleUrls: ['./opening-wallet2.scss']
})
export class OpeningWallet2Page {
  newMemberRealnameAuth: Member = {
    name: '',


  }

  url: string = '';
  authcode: string;
  looptime: number = 60;
  wait: boolean = true;

  resCode: string
  verifyType = VerifyType.wallet
  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public customer: appCommonService,
    public storage: StorageService,
    public common: CommonService,
    public http: MyHttpService
  ) {
  }

  async sendAuthcode() {

    this.resCode = await this.customer.authcode(this.storage.customer.mktId, this.newMemberRealnameAuth.mobi, this.verifyType);
    if (this.resCode) {
      let timmer = setInterval(() => {
        this.looptime -= 1;
        if (this.looptime <= 0) {
          this.wait = true;
          this.looptime = 60;
          clearInterval(timmer);
        }
      }
        , 1000);
    } else {
      this.wait = true;
    }

  }

  async upLoadImg() {
    this.url = await this.common.selectFile();
    let result = await this.common.uploadImage(this.url)
    this.newMemberRealnameAuth.idcardImgUrl = result.fileUrl
  }


  ionViewDidLoad() {
    if (this.storage.newMember) {
      this.newMemberRealnameAuth = this.storage.newMember

    }
  }

  async checkAuthcode() {

    await this.customer.checkAuthcode(this.newMemberRealnameAuth.mobi, this.authcode, this.storage.customer.mktId, this.verifyType)
  }

  async goNext() {
    if (this.url != '') {
      await this.checkAuthcode();
      this.newMemberRealnameAuth.customerId = this.storage.customer.id
      this.navCtrl.push(OpeningWallet3Page, { newMemberRealnameAuth: this.newMemberRealnameAuth })
      this.storage.newMember = this.newMemberRealnameAuth
    } else {
      this.http.createMessage("error", "请补信息")
    }

  }

}



