import { Injectable } from "@angular/core";
import { MyHttpService } from "./http.service";
import { Params } from "../../../share_platform/framework/entity";
import { MemberInvite, Customer } from "../../../share_platform/market/entity";
import { StorageService } from "./storage.service";

@Injectable()
export class appTeamService {
    private api = {
        /**
         * 根据customerId列出team成员
         * get
         * ?memberId&teamId
         */
        teamList: "/app/team/list",
        /**
         * POST
         * body:InviteLog
         */
        inviteCustomer: "/app/team/invite-customer",
        /**
         * 接受加入团队邀请
        GET
        ?inviteLogId&mktId&customerId
         */
        acceptInvite: "/app/team/accept-invite",
        /**
         * 拒绝加入团队邀请
            GET
            ?mktId&inviteLogId&customerId
         */
        refuseInvite: "/app/team/refuse-invite",
        /**
        *
        * get
        * ?mktId&memberId
        *        
         */
        invitelogList: "/app/team/invitelog-list",


        /**
         * 根据手机号查找用户
        *get
        *列出发出去的邀请,时间倒叙排序
         **/
        searchCustomerByPhone: "/app/team/search-customer-by-phone",

        /** 
         * get
         * ?mktId&customerId
        */
        findFreeCustomer: "/app/team/find-free-customer",
        /**
         * exitTeam
         */
        exitTeam: '/app/team/exit',
        /**
         * get ?customerId&commentName&fromMemberId   备注成员昵称
         */
        commmentCustomer: "/app/team/comment-customer",
        /**
         * get ?mktId&customerId 收到的邀请
         */
        recvInvite: "/app/team/recv-invite",
        /**
         * 废弃邀请记录 get ?mktId&customerId&inviteLogId
         * 
         */
        disabledInvite: "/app/team/disabled-invite",

    }
    async disabledInvite(inviteLogId: number, mktId?: number, customerId?: number) {
        mktId = this.sto.member.mktId
        customerId = this.sto.customer.id
        let result = await this.http.Get(this.api.disabledInvite, { params: { inviteLogId, mktId, customerId, } })
        return result
    }
    async recvInvite(mktId?: number, customerId?: number) {
        mktId = this.sto.customer.mktId
        customerId = this.sto.customer.id
        let result = await this.http.Get(this.api.recvInvite, { params: { mktId, customerId } })
        return result.memberInvites
    }
    async commmentCustomer(customerId: number, commentName: string, fromMemberId: number) {
        let result = await this.http.Get(this.api.commmentCustomer, { params: { customerId, commentName, fromMemberId } })
        return result
    }
    async exitTeam(customerId: number, mktId?: number) {
        mktId = this.sto.customer.mktId
        let result = await this.http.Get(this.api.exitTeam, { params: { customerId, mktId } })
        return result
    }
    async findFreeCustomer(mktId: number, customerId: number) {
        let result = await this.http.Get(this.api.findFreeCustomer, { params: { mktId, customerId } })
        return result
    }
    async searchCustomerByPhone(phone: number, mktId?: number) {
        mktId = this.sto.customer.mktId
        let result = await this.http.Get(this.api.searchCustomerByPhone, { params: { phone, mktId } })
        return result.customer
    }
    async invitelogList(mktId?: number, memberId?: number): Promise<MemberInvite[]> {
        mktId = this.sto.customer.mktId
        memberId = this.sto.customer.memberId

        let result = await this.http.Get(this.api.invitelogList, { params: { mktId, memberId } })
        return result.memberInvites
    }
    async refuseInvite(inviteLogId: number, mktId?: number, customerId?: number, ) {
        mktId = this.sto.customer.mktId
        customerId = this.sto.customer.id
        let result = await this.http.Get(this.api.refuseInvite, { params: { inviteLogId, mktId, customerId } })
        return result
    }
    async acceptInvite(inviteLogId: number, mktId?: number, customerId?: number) {
        mktId = this.sto.customer.mktId

        customerId = this.sto.customer.id
        let result = await this.http.Get(this.api.acceptInvite, { params: { inviteLogId, mktId, customerId } })
        return result
    }
    async inviteCustomer(inviteLog: MemberInvite) {

        let result = await this.http.Post(this.api.inviteCustomer, inviteLog)
        return result
    }
    async teamList(memberId?: number, mktId?: number): Promise<Customer[]> {
        mktId = this.sto.customer.mktId;
        memberId = this.sto.customer.memberId

        let result = await this.http.Get(this.api.teamList, { params: { memberId, mktId } });

        return result.customers

    }
    constructor(public http: MyHttpService, public sto: StorageService) { }
}


