import { Component, Input, ViewChild, ElementRef } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

import * as echarts from "echarts";
/**
 * Generated class for the StatisticalMoneyPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

// @IonicPage()
@Component({
  selector: 'page-statistical-money',
  templateUrl: 'statistical-money.html',
})
export class StatisticalMoneyPage {
  @Input() width: string = '80%';
  @Input() height: string = '400px'; //ElementRef
  @ViewChild('chart') chart: ElementRef;
  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    // 指定图表的配置项和数据
    var option = {
      title: {
        text: ''
      },
      tooltip: {},
      legend: {
        data: ['交易金额走势/元']
      },
      xAxis: {
        data: [75, 49, 45, 27, 26, 21, 19]
      },
      yAxis: {},
      series: [{
        name: '交易金额走势/元',
        type: 'line',
        data: [75, 49, 45, 27, 26, 21, 19]
      }]
    };

    // 基于准备好的dom，初始化echarts实例.init(this.chart.nativeElement);
    let myChart = echarts.init(this.chart.nativeElement);
    // 使用刚指定的配置项和数据显示图表。
    myChart.setOption(option);
  }

  getEchartsStyles() {
    return {
      width: this.width,
      height: this.height
    };
  }
}



