import { Pipe, PipeTransform } from '@angular/core';
import { MemberInviteStatus } from '../../../share_platform/market/enum';
/*
 * Raise the value exponentially
 * Takes an exponent argument that defaults to 1.
 * Usage:
 *   value | exponentialStrength:exponent
 * Example:
 *   {{ 2 | exponentialStrength:10 }}
 *   formats to: 1024
*/
@Pipe({ name: 'inviPipePipe' })
export class InviPipePipe implements PipeTransform {
    inviType: MemberInviteStatus
    transform(value: MemberInviteStatus, exponent: string) {






        if (value == MemberInviteStatus.Active) {


            return "待处理";


        }

        if (value == MemberInviteStatus.Agree) {
            return "已同意"
        }
        if (value == MemberInviteStatus.Del) {
            return "已取消"
        } if (value == MemberInviteStatus.Refuse) {
            return "拒绝"
        }


        // switch (value) {
        //     case MemberInviteStatus.Active:
        //         "待处理";
        //         break;
        //     case MemberInviteStatus.Refuse:
        //         "已拒绝";
        //         break;
        //     case MemberInviteStatus.Agree:
        //         "已同意"
        //         break;



        //     default:
        //         "已删除"
        // }

    }
}


