import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { WalletTransferDetailPage } from '../wallet-transfer-detail/wallet-transfer-detail';
import { appMemberAccountService } from '../../app/share/service/app_member_account.service';
import { StorageService } from '../../app/share/service/storage.service';
import { Member } from '../../share_platform/market/entity';
import { WalletTransferSerchPage } from '../wallet-transfer-serch/wallet-transfer-serch';

/**
 * Generated class for the WalletTransferListPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-wallet-transfer-list',
  templateUrl: 'wallet-transfer-list.html',
})
export class WalletTransferListPage {
  friends: Member[]
  constructor(public navCtrl: NavController, public navParams: NavParams,
    public memSer: appMemberAccountService,
    public sto: StorageService
  ) {
  }

  async ionViewDidLoad() {
    let result = await this.memSer.memberFriends(this.sto.member.id)
    this.friends = result
  }
  addTransfer() {
    this.navCtrl.push(WalletTransferSerchPage)
  }
  transMoney(data) {
    this.navCtrl.push(WalletTransferDetailPage, data)
  }
}



