import { Component, Input } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { Customer } from '../../share_platform/market/entity';
import { appTeamService } from '../../app/share/service/app_team.service';
import { MyHttpService } from '../../app/share/service/http.service';

/**
 * Generated class for the Register1Page page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

// @IonicPage()

@Component({
    selector: 'page-nickName',

    templateUrl: 'nickName.html',
})
export class NickNamePage {

    customer: Customer
    constructor(public navCtrl: NavController,
        public navParams: NavParams,
        public teamServvice: appTeamService,
        public http: MyHttpService

    ) {
        this.customer = this.navParams.data
    }

    ionViewDidLoad() {
        console.log('ionViewDidLoad Register1Page');
        // this.customer.marketList().then(list => this.marketList = list);

    }

    async save() {
        await this.teamServvice.commmentCustomer(this.customer.id, this.customer.teamComment, this.customer.memberId)
        this.http.createMessage("success", "修改成功")
        this.navCtrl.pop()
    }
}



