import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { appMemberAccountService } from '../../app/share/service/app_member_account.service';
import { friendMsgType, MemberInviteStatus } from '../../share_platform/market/enum';
import { Member, ContactInvite } from '../../share_platform/market/entity';
import { StorageService } from '../../app/share/service/storage.service';
import { WalletFriendDetailPage } from '../wallet-friend-detail/wallet-friend-detail';
import { MyHttpService } from '../../app/share/service/http.service';

/**
 * Generated class for the WalletFriendPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-wallet-friend',
  templateUrl: 'wallet-friend.html',
})
export class WalletFriendPage {

  isAndroid: boolean = false;

  msgType = friendMsgType
  selectType: string = "send";
  InviLists: ContactInvite[]
  defulStatus = MemberInviteStatus
  reciveLists: ContactInvite[]
  constructor(public navCtrl: NavController, public navParams: NavParams,
    public memS: appMemberAccountService,
    public sto: StorageService,
    public http: MyHttpService



  ) {

  }

  async ionViewDidLoad() {
    await this.sendList()
  }

  /**发出的邀请 */
  async sendList() {
    let result = await this.memS.memberInviList(this.sto.member.id, this.msgType.Send)
    if (result) {
      this.InviLists = result.contactInvite
    } else {
      this.http.createMessage("warning", "暂无数据")
    }

  }
  /**收到的邀请 */
  async reciveList() {
    let result = await this.memS.memberInviList(this.sto.member.id, this.msgType.Receive)
    if (result) {
      this.reciveLists = result.contactInvite
    }

  }
  /**详情  同意或拒绝*/
  async goDetail(data) {
    this.navCtrl.push(WalletFriendDetailPage, data)
  }
}



