import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { PersonInfoPage } from "../person-info/person-info"

import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { appCommonService } from '../../app/share/service/app_common.service';
import { appCustomerService } from '../../app/share/service/app_customer.service';
import { MyHttpService } from '../../app/share/service/http.service';
/**
 * Generated class for the ModifyPasswordPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

// @IonicPage()

@Component({
  selector: 'page-modify-password',
  templateUrl: 'modify-password.html',
})
export class ModifyPasswordPage {

  modifyPassworForm: FormGroup;
  oldPassword: any;
  newPassword: any;
  confirmPassword: any;
  constructor(public navCtrl: NavController, public navParams: NavParams, public fb: FormBuilder,
    public customer: appCustomerService,
    public http: MyHttpService
  ) {

    // this.modifyPassworForm = this.fb.group({
    //   oldPassword: ['', Validators.compose([Validators.required])],
    //   newPassword: ['', Validators.compose([Validators.required, Validators.minLength(6)])],
    //   confirmPassword: ['', [this.confirmValidator]]
    // });
    // this.oldPassword = this.modifyPassworForm.controls['oldPassword'];
    // this.newPassword = this.modifyPassworForm.controls['newPassword'];
    // this.confirmPassword = this.modifyPassworForm.controls['confirmPassword'];
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ModifyPasswordPage');
  }

  // confirmValidator = (control: FormControl): { [s: string]: boolean } => {
  //   if (!control.value) {
  //     return { required: true };
  //   } else if (control.value !== this.modifyPassworForm.controls.newPassword.value) {
  //     return { confirm: true, error: true };
  //   }
  // };


  async modifyPassword() {


    let passwordRex = /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,}$/
    //let test = ( || !(passwordRex.test(value.newPassword) || !(passwordRex.test(value.confirmPassword))
    // if (test) {
    //   this.customer.http.createMessage("error", "密码至少八个字符，至少一个字母和一个数字")
    // }
    if (!(passwordRex.test(this.oldPassword)) || !(passwordRex.test(this.newPassword)) || !(passwordRex.test(this.confirmPassword))) {
      this.customer.http.createMessage("error", "密码至少八个字符，至少一个字母和一个数字")
    } else {
      if (this.newPassword !== this.confirmPassword) {
        this.http.createMessage("warning", "两次密码不一致，请重新输入")
      } else {
        let result = await this.customer.modifyPassword(this.oldPassword, this.newPassword);
        if (result) {
          this.http.createMessage("success", "修改成功")
          this.navCtrl.setRoot(PersonInfoPage)

        }
      }
    }



  }
  goNext() {

  }
}



