import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Events } from 'ionic-angular';
import { CustomerPage } from '../customer/customer';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { appProductOrderService } from '../../app/share/service/app_ProductOrder.service';
import { ModalController } from 'ionic-angular';
import { queryOrderStatus, PayTypeEnum, OrderStatusEnum } from '../../share_platform/market/enum';
import { Http } from '@angular/http';
import { MyHttpService } from '../../app/share/service/http.service';
import { Observable } from "RxJS/Rx";
import { BLE } from '@ionic-native/ble';



/**
 * Generated class for the QrCodePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

// @IonicPage()
@Component({
  selector: 'page-qr-code',
  templateUrl: 'qr-code.html',
})
export class QrCodePage {

  qrCodeUrl;
  createdCode = null;
  orderNo: string = '';
  show: boolean = false;
  qryBuyerCreditStatus;
  queryOrderStatus;
  credit;
  payType;
  pageView: boolean = true;



  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public productOrderService: appProductOrderService,
    public modelCtrl: ModalController,
    public http: MyHttpService,
    public event: Events,
    public ble: BLE,

  ) {
    let data = this.navParams.get('data');
    console.log(data)
    this.payType = this.navParams.get('payType');
    this.createdCode = data.qCodeBase64Str
    this.orderNo = data.orderNo;
    console.log(data)

    //
    if (this.payType == PayTypeEnum.FS_CREDIT) {
      //rx 轮询订阅  买家信用
      this.qryBuyerCreditStatus = Observable.interval(5000).startWith(1) //消除延时
        .mergeMap(() => this.productOrderService.qryBuyerCredit(this.orderNo))
        .subscribe((next) => {
          if (next.memberName != null) {
            this.show = true; //打开模态框
            this.credit = next;
            this.qryBuyerCreditStatus.unsubscribe(); //取消订阅 来停止轮询
          }
          if (this.pageView == false) {
            this.qryBuyerCreditStatus.unsubscribe()
          }

        })

    } else if (this.payType == PayTypeEnum.FS_C2B) {
      this.queryOrderStatus = Observable.interval(5000).startWith(1) //消除延时
        .mergeMap(() => this.productOrderService.querOrderStatus(this.orderNo))
        .subscribe((next) => {
          console.log(next.status)
          if (next.status == OrderStatusEnum.PAID) {
            console.log('交易成功')
            this.show = false; //关闭模态框
            this.http.createMessage('success', '交易成功')
            this.queryOrderStatus.unsubscribe(); //取消订阅 来停止轮询
            this.printer();
          } else if (next.qcodeBizType == OrderStatusEnum.CLOSED) {
            this.http.createMessage('warning', '交易关闭');
          }

          if (this.pageView == false) {
            this.qryBuyerCreditStatus.unsubscribe()
          }
        })
    }
  }


  async agree(status) {
    status = (status == 'SELLER_AGREE') ? queryOrderStatus.SELLER_AGREE : queryOrderStatus.CLOSED;
    let res = await this.productOrderService.changeStatus(this.orderNo, status);
    if (res && status == 'SELLER_AGREE') {
      this.show = false;
      //轮询 订阅订单状态
      this.queryOrderStatus = Observable.interval(1000).startWith(1) //消除延时
        .mergeMap(() => this.productOrderService.querOrderStatus(this.orderNo))
        .subscribe((next) => {
          if (next.status == OrderStatusEnum.PAID) {
            this.queryOrderStatus.unsubscribe(); //取消订阅 来停止轮询
            this.show = false;
            this.http.createMessage('success', '交易成功')
            this.printer();
          }
          if (this.pageView == false) {
            this.queryOrderStatus.unsubscribe()
          }
        })
    }
  }

  ionViewDidLeave() {
    this.pageView = false;
  }



  //打印机
  printer() {
    this.event.subscribe('printer', device => {
      this.ble.isConnected(device.id).then(() => {
        alert('开始打印')
        // let buffer = this.productOrderService.xxx()

        // this.ble.write(device.id, 'FF10', 'FF11', [hellow])
      })
    })
  }


  ionViewDidLoad() {
    console.log('ionViewDidLoad QrCodePage');
  }

  customerList() {
    this.navCtrl.push(CustomerPage);
  }

}



