import { MyHttpService } from "./http.service";
import { StorageService } from "./storage.service";
import { Injectable } from "@angular/core";
import { Customer } from "../../../share_platform/market/entity";

@Injectable()
export class appCustomerService {
    private api = {
        /**
         * 登陆
         * post:
         * body:{phone,password,mktId}
         * return {customer,token}
         */
        login: "/customer/login",
        /**
         * 忘记密码
         * post:
         * body:customer
         */

        forgotPassword: '/app/customer/forgot-password',


        /**
         * get
         * ?mktId&phone
         */

        findbyMobi: "/findbyMobi",
        /**
         * get
         * ?mktId&customerId
         * 
         */
        customerDetail: '/app/customer/detail',
        /**
         * 验证customer密码是否正确
         * post
         * body:customer
         */
        authPassword: '/app/customer/auth-password',
        /**
         * 修改手机
         * get
         * ?newPone&authcode&mktId&customerId
         */
        modifyPhone: '/app/customer/modify-phone',
        /**
         * post
         * body:Customer,
         * ?mktId
         */
        signup: '/customer/signup',
        /**修改登录密码 */
        modifyPassword: '/app/customer/modifyPassword',
        /**
        * 刷新用户设备id get: ?registrationId&customerId&mktId
        */
        updateRegistrationId: "/app/customer/update-registrationId",


    }

    async updateRegistrationId(registrationId: string) {
        let result = await this.http.Get(this.api.updateRegistrationId, { params: { registrationId, customerId: this.storage.customer.id } })
        return result
    }

    async findbyMobi(mktId: number, phone: string) {
        let result = await this.http.Get(this.api.findbyMobi, { params: { mktId, phone } })
        return result
    }

    async modifyPhone(oldPhone: string, newPhone: string, authCode: string, mktId: number, customerId: number) {
        let result = await this.http.Get(this.api.modifyPhone, { params: { oldPhone, newPhone, authCode, mktId, customerId } })
        return result
    }
    async authPassword(customer: Customer) {

        let result = await this.http.Post(this.api.authPassword, customer)
        return result;
    }

    async customerDetail(customerId: number, mktId?: number, ) {
        mktId = this.storage.customer.mktId
        let result = await this.http.Get(this.api.customerDetail, { params: { customerId, mktId } })
        return result.customer
    }
    modifyPassword(oldPassword: string, newPassword: string) {
        let customerId = this.storage.customer.id
        this.http.Get(this.api.modifyPassword, { params: { oldPassword, newPassword, customerId } });
    }
    async signup(newCustomer: Customer, authcode: string) {
        let result = await this.http.Post(this.api.signup, newCustomer, { params: { authcode } });
        if (result.customer) {
            return result.customer;
        }
    }
    async forgotPassword(modifyCustomer: Customer, authcode) {

        return this.http.Post(this.api.forgotPassword, modifyCustomer, { params: { authcode } });
    }
    async login(userName: string, password: string, mktId) {
        let result = await this.http.Post(this.api.login, { userName, password, mktId });
        if (result) {
            return result;
        }
    }
    constructor(public http: MyHttpService, public storage: StorageService) {

    }
}


