import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ForgetPassword2Page } from "../forget-password2/forget-password2"
import { appCustomerService } from '../../../app/share/service/app_customer.service';
import { appCommonService } from '../../../app/share/service/app_common.service';
import { Market } from '../../../share_platform/framework/entity';
import { VerifyType } from '../../../share_platform/framework/enum';
import { StorageService } from '../../../app/share/service/storage.service';
import { MyHttpService } from '../../../app/share/service/http.service';
/**
 * Generated class for the ForgetPassword1Page page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

// @IonicPage()
@Component({
  selector: 'page-forget-password1',
  templateUrl: 'forget-password1.html',
})

export class ForgetPassword1Page {
  sendBool: boolean = true;
  mktId: number;
  authcode
  looptime: number = 60;
  marketList: Market[] = [];
  phone: string = '';
  simpleColumns: any[];
  data;
  verifyType = VerifyType.reg
  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public customer: appCustomerService,
    public comm: appCommonService,
    public sto: StorageService,
    public http: MyHttpService

  ) {


  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ForgetPassword1Page');
    this.comm.marketList().then(list => this.marketList = list);
  }

  async  sendAuthcode() {
    if (!this.phone) {
      this.http.createMessage("error", "请输入手机号")
    } else {
      let result = await this.comm.authcode(this.sto.market.mktId, this.phone, this.verifyType);
      if (result) {
        this.looptime = 60;
        let timmer = setInterval(() => {
          this.looptime -= 1;
          if (this.looptime <= 0) {
            this.sendBool = true
            clearInterval(timmer);
          }

        }, 1000);
      } else {
        this.sendBool = true
      }
    }

  }
  async goNext() {
    if (this.authcode && this.phone) {

      let result = await this.comm.checkAuthcode(this.phone, this.authcode, this.sto.market.mktId, this.verifyType);
      if (result) {
        this.navCtrl.push(ForgetPassword2Page, { authcode: this.authcode, phone: this.phone, });
      }
      else {
        this.customer.http.createMessage('error', '参数不全')
      }

    }





  }
}



