import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Card } from 'ionic-angular';
import { StorageService } from '../../app/share/service/storage.service';
import { Customer, CustCard } from '../../share_platform/market/entity';
import { appCustomerService } from '../../app/share/service/app_customer.service';
import { appMemberAccountService } from '../../app/share/service/app_member_account.service';
import { MyHttpService } from '../../app/share/service/http.service';

/**
 * Generated class for the WalletCardPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */
enum ViewState {
  Open,
  Untied

}
@IonicPage()
@Component({
  selector: 'page-wallet-card',
  templateUrl: 'wallet-card.html',
})
export class WalletCardPage {
  card: CustCard = {

  }
  ViewState = ViewState;
  state
  cus: Customer
  cardSto: CustCard
  constructor(public navCtrl: NavController, public navParams: NavParams,
    public sto: StorageService,
    public memSer: appMemberAccountService,
    public http: MyHttpService
  ) {
    this.cus = this.sto.customer
  }

  ionViewDidLoad() {

  }
  ionViewCanEnter() {
    if (this.sto.card.id) {
      this.cardSto = this.sto.card
      this.state = ViewState.Untied

    } else {
      this.state = ViewState.Open
    }
  }

  async  goNext() {
    this.cus.card = this.card

    let result = await this.memSer.relationCard(this.cus)
    this.sto.card = {}
    this.sto.card = result.customerDB.card
    this.http.createMessage("success", "创建成功")
    this.state = ViewState.Untied

  }
  //解绑会员卡
  async unTied() {

    let result = await this.memSer.relieveCard(this.sto.card.no)
    if (result) {
      this.http.createMessage("success", "解绑成功")
      this.sto.card = {}
      this.state = ViewState.Open
    }
  }
}



