import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { appMemberAccountService } from '../../app/share/service/app_member_account.service';
import { Member, ContactInvite } from '../../share_platform/market/entity';
import { StorageService } from '../../app/share/service/storage.service';
import { MyHttpService } from '../../app/share/service/http.service';

/**
 * Generated class for the WalletFriendAddPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */
enum View {
  List,
  Add,

}
@IonicPage()
@Component({
  selector: 'page-wallet-friend-add',
  templateUrl: 'wallet-friend-add.html',
})
export class WalletFriendAddPage {
  keyWord: string
  reciMembers: Member[]
  View = View
  state
  reciMember: Member
  newInvi: ContactInvite = {
    content: ""
  }
  constructor(public navCtrl: NavController, public navParams: NavParams,
    public memSer: appMemberAccountService,
    public sto: StorageService,
    public http: MyHttpService
  ) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad WalletFriendAddPage');
  }
  async serchKeyWord($event) {

    let key_code = window.event ? $event.keyCode : $event.which;
    if (key_code == 13) {
      this.serch()
    }


  }
  async serch() {
    let result = await this.memSer.searchMember(this.keyWord)
    if (result.member == []) {
      this.http.createMessage("warning", "暂无数据")
    } else {


      this.reciMembers = result.member
      this.state = View.List
    }

  }
  async toAdd(reciMember) {
    this.reciMember = reciMember
    this.state = View.Add

  }
  async ok() {
    let result = await this.memSer.memberFriendInvi(this.sto.member.id, this.reciMember.id, this.newInvi.content)
    if (result) {
      this.http.createMessage("success", "邀请已发送，等待对方同意")
      this.navCtrl.pop()
    }
    else {
      this.state = View.List
    }
  }
}



