import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Member } from '../../share_platform/market/entity';
import { WalletFriendPage } from '../wallet-friend/wallet-friend';
import { WalletFriendAddPage } from '../wallet-friend-add/wallet-friend-add';
import { StorageService } from '../../app/share/service/storage.service';
import { appMemberAccountService } from '../../app/share/service/app_member_account.service';
import { WalletTransferDetailPage } from '../wallet-transfer-detail/wallet-transfer-detail';

/**
 * Generated class for the WalletFriendListPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-wallet-friend-list',
  templateUrl: 'wallet-friend-list.html',
})
export class WalletFriendListPage {
  friends: Member[] = []
  constructor(public navCtrl: NavController, public navParams: NavParams,
    public sto: StorageService,
    public memS: appMemberAccountService
  ) {
  }

  async ionViewDidLoad() {
    let result = await this.memS.memberFriends(this.sto.member.id)

    this.friends = result

  }
  toTrans(data) {//WalletTransferDetailPage
    this.navCtrl.push(WalletTransferDetailPage, data)
  }
  friendMess() {
    this.navCtrl.push(WalletFriendPage)
  }
  friendAdd() {
    this.navCtrl.push(WalletFriendAddPage)
  }


  doRefresh(refresher) {
    console.log('Begin async operation', refresher);

    setTimeout(() => {
      console.log('Async operation has ended');



      refresher.complete();
    }, 2000);
  }

  doInfinite(infiniteScroll) {
    console.log('Begin async operation');

    setTimeout(() => {
      for (let i = 0; i < 10; i++) {

      }

      console.log('Async operation has ended');
      infiniteScroll.complete();
    }, 500);
  }




}



