import { Injectable } from "@angular/core";
import { MyHttpService } from "./http.service";

@Injectable()
export class appAnalysisService {
    private api = {
        /**
        * get
        * ?mktId&memberId
        */
        todayInfo: '/app/analysis/todayInfo',

    }
    async todayInfo(mktId: number, memberId: number) {
        return this.http.Get(this.api.todayInfo, { params: { mktId, memberId } })


    }
    constructor(public http: MyHttpService) { }
}


