import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { SignInPage } from "../sign-in/sign-in"
import { appCustomerService } from '../../../app/share/service/app_customer.service';
import { Customer } from '../../../share_platform/market/entity';
import { CustomerStatusEnum, CreateWayEnum } from '../../../share_platform/market/enum';
/** 
 * Generated class for the RegisterDetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

// @IonicPage()
@Component({
  selector: 'page-register-detail',
  templateUrl: 'register-detail.html',
})
export class RegisterDetailPage {

  repeatPassword: string;
  authcode: string = "";
  newCustomer: Customer = {
    createWay: CreateWayEnum.APP,

    password: '',
    mobi: '',

  }

  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public customer: appCustomerService) {
    this.newCustomer.mobi = this.navParams.data.phone;
    this.newCustomer.mktId = this.navParams.data.mktId;
    this.authcode = this.navParams.data.authcode;
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad RegisterDetailPage');
  }
  async goNext() {
    if (this.newCustomer.mobi && this.newCustomer.name && this.newCustomer.password) {
      let passwordRex = /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,}$/
      if (!(passwordRex.test(this.newCustomer.password))) {
        this.customer.http.createMessage("error", "密码至少八个字符，至少一个字母和一个数字")
      } else {
        if (this.newCustomer.password == this.repeatPassword) {
          let customer = await this.customer.signup(this.newCustomer, this.authcode);
          if (customer) {
            this.customer.http.createMessage('success', "注册成功");
            this.navCtrl.push(SignInPage);
          }
        } else {
          this.customer.http.createMessage('error', '密码不一致');
        }
      }


    } else {
      this.customer.http.createMessage('error', '信息不完善');
    }

    // this.navCtrl.setRoot(SignInPage)
  }

}



