import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { MarketPage } from "../../market/market";
import { OpenBillPage } from "../../open-bill/open-bill";

import { MyTransactionPage } from "../../my-transaction/my-transaction";//我的交易
import { StatisticalsPage } from "../../statisticals/statisticals";//统计分析
import { CreditPage } from "../../credit/credit";//赊销记录
import { ProductListPage } from "../../product-list/product-list";//商品管理列表

import { ComeOutPage } from "../../come-out/come-out";//出厂申请
import { ArrearsSumPage } from "../../arrears-sum/arrears-sum";//欠款汇总
import { UnPayPage } from "../../un-pay/un-pay";//我的待付款
import { RepaymentPage } from "../../repayment/repayment";//还款记录
import { BarcodeScannerPage } from '../../barcode-scanner/barcode-scanner';
import { appCustomerService } from '../../../app/share/service/app_customer.service';
import { StorageService } from '../../../app/share/service/storage.service';
import { Member, Customer, TransArea } from '../../../share_platform/market/entity';
import { MyHttpService } from '../../../app/share/service/http.service';
import { appProductOrderService } from '../../../app/share/service/app_ProductOrder.service';
import { viewClassName } from '@angular/compiler';
import { RepaymentListPage } from '../../repayment-list/repayment-list';
enum View {
  Detail,
  selectTxnList,
}
@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
  View = View;
  state = View.Detail;
  member: Member
  customer: Customer
  currentStatus: boolean
  txns: TransArea[] = []
  userStatus = this.sto.customer.memberId
  selectTxn: TransArea
  constructor(public navCtrl: NavController,
    public sto: StorageService,
    public http: MyHttpService,
    public orderService: appProductOrderService
  ) {



  }

  ionViewDidEnter() {
    console.log("b")
    this.customer = this.sto.customer
    if (this.customer.memberId) {
      this.member = this.sto.member
    }
  }
  async ionViewCanEnter() {


  }
  ionViewCanLeave() {
    if (!this.sto.customer.memberId) {
      if (this.currentStatus) {
        this.currentStatus = false;
        return true
      } else {
        this.http.createMessage("warning", "暂无权限")
        return false
      }
    } else {
      return true
    }

  }
  goMarket() {
    this.currentStatus = true
    this.navCtrl.push(MarketPage)
  }

  barcodeScanner() {
    this.navCtrl.push(BarcodeScannerPage)
  }
  async OpenBill() {

    //this.http.createMessage("warning", "请选择您的交易区")
    //await this.allTxn()

    this.navCtrl.push(OpenBillPage)

  }
  goMyTransactionPage() {

    this.navCtrl.push(MyTransactionPage)

  }
  goStatisticalsPage() {

    this.navCtrl.push(StatisticalsPage)

  }
  goCreditPage() {

    this.navCtrl.push(CreditPage)

  }
  goRepaymentPage() {

    this.navCtrl.push(RepaymentListPage)

  }
  goProductListPage() {


    this.navCtrl.push(ProductListPage)

  }
  goComeOutPage() {

    this.navCtrl.push(ComeOutPage)

  }
  goArrearsSumPage() {

    this.navCtrl.push(ArrearsSumPage)

  }
  goUnPayPage() {

    this.navCtrl.push(UnPayPage)

  }


}



