import { Component } from '@angular/core';
import { NavController, NavParams, Events } from 'ionic-angular';
import { Product, TransArea, ProdCatalog } from '../../share_platform/market/entity';
import { appProductOrderService } from '../../app/share/service/app_ProductOrder.service';
import { PriceUnitEnum, PriceWayEnum } from '../../share_platform/market/enum';
import { MyHttpService } from '../../app/share/service/http.service';
enum View {
  classDetail,
  selectClass,
  selectTxnList,
}
/**
 * Generated class for the ProductAPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

// @IonicPage()

@Component({
  selector: 'page-product-edit',
  templateUrl: 'product-edit.html',
})
export class ProductEditPage {
  View = View;
  state = View.classDetail;


  prodCatalogs: ProdCatalog[];
  childProducts: ProdCatalog[];
  selectProCate: ProdCatalog//选中的分类名称
  newProduct: Product = {
    name: "",
  }

  txns: TransArea[] = []
  selectProductValuationType
  /**选择的计价方式 计件 计重 */
  priceType = PriceWayEnum
  selectTxn: TransArea
  product: Product
  isShow: boolean
  mapProducts: ProdCatalog[][] = [];
  constructor(public navCtrl: NavController, public navParams: NavParams,
    public prod: appProductOrderService,
    public orderService: appProductOrderService,
    public http: MyHttpService


  ) {

  }
  ionViewCanEnter() {
    this.product = this.navParams.data
    this.selectProductValuationType = this.product.priceWay
    this.selectProCate = this.product.prodCat
    this.selectTxn = this.product.prodCat.txn

  }
  selectOk(txn) {
    this.selectTxn = txn
    this.state = View.classDetail
  }
  cancel() {
    this.selectProCate = {}
  }
  async categoryList() {
    this.mapProducts = [];
    // 展开一级商品分类
    if (this.selectTxn) {
      this.state = View.selectClass;
      let result = await this.prod.productCatList(this.selectTxn.txnId);
      this.prodCatalogs = result;
      this.mapProducts.push(result);
      console.log(this.mapProducts)


    } else {
      this.http.createMessage("warning", "请先选择交易区")
    }

  }
  async selectChild(data, arrIndex: number, prodIndex: number) {
    if (arrIndex != this.mapProducts.length - 1) {
      this.mapProducts.splice(arrIndex + 1);
    }

    this.childProducts = await this.prod.productCatList(this.selectTxn.txnId, data.id);


    if (this.childProducts.length > 0) {
      this.mapProducts.push(JSON.parse(JSON.stringify(this.childProducts)));
      this.isShow = false
    } else {
      this.isShow = true
    }

    this.selectProCate = data;

    this.mapProducts[arrIndex].forEach(product => product.checked = false);
    this.selectProCate.checked = true;

  }
  ionViewDidLoad() {

  }
  async allTxn() {
    this.txns = await this.orderService.txnList()
  }
  async  save() {
    this.product.priceWay = this.selectProductValuationType
    this.product.prodCat = this.selectProCate
    await this.prod.productUpdate(this.product)
    this.http.createMessage("success", "更新成功")
    this.navCtrl.pop()
  }




}



