import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { appTeamService } from '../../app/share/service/app_team.service';
import { MemberInvite, Member, Customer } from '../../share_platform/market/entity';
import { StorageService } from '../../app/share/service/storage.service';
import { CommonService } from '../../app/share/service/common.service';
import { MemberInviteStatus } from '../../share_platform/market/enum';

/**
 * Generated class for the InvitationPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

// @IonicPage()
@Component({
  selector: 'page-invitation',
  templateUrl: 'invitation.html',
})
export class InvitationPage {
  invitelogs: MemberInvite[]
  invitelog: MemberInvite
  title: string
  member: Member
  customer: Customer
  receLogs: MemberInvite[]
  selecttLog: MemberInvite
  empSelecttLog: MemberInvite
  userRole: any

  selectType = MemberInviteStatus
  constructor(public navCtrl: NavController, public navParams: NavParams,
    public teamService: appTeamService,
    public sto: StorageService,
    public comm: CommonService
  ) {
    this.title = this.navParams.data.title;
  }

  async ionViewDidLoad() {
    this.customer = this.sto.customer

    if (this.customer.memberId) {
      this.member = this.sto.member;
      if (this.member.id == this.customer.memberId) {
        this.invitelogs = await this.teamService.invitelogList()

      }
    } else {
      this.receLogs = await this.teamService.recvInvite()

    }







  }
  selectInv(invitelog) {
    if (invitelog.status == this.selectType.Active) {


      this.selecttLog = invitelog
      this.invitelogs = []
    }
  }
  empSelectInv(data) {
    if (data.status == this.selectType.Active) {
      this.selecttLog = data
      this.receLogs = []
    }
  }
  async deleteInvi(invitelog) {
    this.invitelog = invitelog
    await this.teamService.disabledInvite(this.invitelog.id)
  }
}



