import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { appMemberAccountService } from '../../app/share/service/app_member_account.service';
import { Member } from '../../share_platform/market/entity';
import { WalletTransferDetailPage } from '../wallet-transfer-detail/wallet-transfer-detail';

/**
 * Generated class for the WalletTransferSerchPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-wallet-transfer-serch',
  templateUrl: 'wallet-transfer-serch.html',
})
export class WalletTransferSerchPage {
  keyWord: string
  reciMembers: Member[]
  reciMember: Member
  constructor(public navCtrl: NavController, public navParams: NavParams,
    public memSer: appMemberAccountService

  ) {
  }
  /** 关键字搜索*/
  async serchKeyWord($event) {

    let key_code = window.event ? $event.keyCode : $event.which;
    if (key_code == 13) {
      this.serch()
    }


  }
  async serch() {
    let result = await this.memSer.searchMember(this.keyWord)

    this.reciMembers = result.member

  }
  async toTrans(reciMember) {
    this.reciMember = reciMember
    this.navCtrl.push(WalletTransferDetailPage, this.reciMember)
  }
  ionViewDidLoad() {
    console.log('ionViewDidLoad WalletTransferSerchPage');
  }

}



