import { Component } from '@angular/core';
import { NavController, NavParams, Events, IonicPage } from 'ionic-angular';
import { Product, ProdCatalog, TransArea } from '../../share_platform/market/entity';
import { appProductOrderService } from '../../app/share/service/app_ProductOrder.service';
import { PriceUnitEnum, PriceWayEnum } from '../../share_platform/market/enum';
import { StorageService } from '../../app/share/service/storage.service';
import { MyHttpService } from '../../app/share/service/http.service';

enum View {
  classDetail,
  selectClass,
  selectTxnList,
}
/**
 * Generated class for the ProductAddPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

// @IonicPage()
@Component({
  selector: 'page-product-add',
  templateUrl: 'product-add.html',
})
export class ProductAddPage {
  View = View;
  state = View.classDetail;


  prodCatalogs: ProdCatalog[];
  childProducts: ProdCatalog[];
  mapProducts: ProdCatalog[][] = [];
  selectProCate: ProdCatalog//选中的分类名称
  newProduct: Product = {
    name: "",
  }

  txns: TransArea[] = []
  selectProductValuationType
  priceType = PriceWayEnum
  selectTxn: TransArea
  prodCatalogs2: ProdCatalog[]
  prodCatalogs3: ProdCatalog[] = [];
  isShow: boolean
  selectpriceUnit
  constructor(public navCtrl: NavController, public navParams: NavParams,
    public events: Events,
    public prod: appProductOrderService,
    public orderService: appProductOrderService,
    public stro: StorageService,
    public http: MyHttpService

  ) {

    // this.events.subscribe("data", (categoryA) => {

    //   this.categoryA = categoryA
    //   this.events.unsubscribe('data');
    // })


  }
  selectOk(txn) {
    this.selectTxn = txn
    this.state = View.classDetail
  }



  async categoryList() {
    this.mapProducts = [];
    // 展开一级商品分类
    if (this.selectTxn) {
      this.state = View.selectClass;
      let result = await this.prod.productCatList(this.selectTxn.txnId);
      this.prodCatalogs = result;
      this.mapProducts.push(result);
      console.log(this.mapProducts)


    } else {
      this.http.createMessage("warning", "请先选择交易区")
    }

  }

  ionViewDidLoad() {

  }
  async allTxn() {
    this.txns = await this.orderService.txnList()
  }
  async  addProduct() {
    this.newProduct.custId = this.stro.customer.id
    this.newProduct.memberId = this.stro.customer.memberId
    this.newProduct.prodCatId = this.selectProCate.id
    this.newProduct.priceWay = this.selectProductValuationType
    this.selectpriceUnit = this.stro.priceW
    this.newProduct.priceUnit = this.selectpriceUnit
    debugger
    await this.prod.productCreate(this.newProduct)
    this.http.createMessage("success", "创建商品成功")
    this.selectTxn = {}
    this.selectProductValuationType = {}
    this.newProduct.price = 0
    this.selectProCate = {}
    this.newProduct.name = ""
  }
  async selectChild(data, arrIndex: number, prodIndex: number) {
    if (arrIndex != this.mapProducts.length - 1) {
      this.mapProducts.splice(arrIndex + 1);
    }

    this.childProducts = await this.prod.productCatList(this.selectTxn.txnId, data.id);


    if (this.childProducts.length > 0) {
      this.mapProducts.push(JSON.parse(JSON.stringify(this.childProducts)));
      this.isShow = false
    } else {
      this.isShow = true
    }

    this.selectProCate = data;

    this.mapProducts[arrIndex].forEach(product => product.checked = false);
    this.selectProCate.checked = true;

  }

  cancel() {
    this.selectProCate = {}
  }

}



